import request from '@/utils/request'

//  修改疫苗接种状态
export function changeStatus(data) {
  return request({
    url: `/family_member_vaccine/change_status`,
    method: 'put',
    data
  })
}
// 关闭健康管理任务
export function closeTask(taskId, data) {
  return request({
    url: `/task_manager/${taskId}/close`,
    method: 'put',
    data
  })
}
// 取消发送
export function cancelTask(taskId) {
  return request({
    url: `/task_manager/cancelTask/${taskId}`,
    method: 'post'
    // mock: true
  })
}
// 为用户留言添加回复
export function replayUser(taskId, data) {
  return request({
    url: `/task_manager/replyUser/${taskId}`,
    method: 'post',
    data: data
    // mock: true
  })
}
