<template>
  <div id="app">
    <div class="build-id">Last build version: {{ buildId }}</div>
    <router-view />
    <div
      v-if="$store.state.user.mts_user.type === 'trainee'"
      class="shortcut-menu"
    >
      <ul class="list">
        <li class="keys" @click="$router.push('/study/shortcut-menu')">
          <!--<img src="./assets/images/kuaijiejian-@2x.png" alt="快捷键">-->
          <div class="image shortcut-img" />
          <span>快捷键</span>
        </li>
        <li class="plan" @click="$router.push('/study/my-plan')">
          <!--<img src="@/assets/images/icon_dingtab1@2x.png" alt="我的计划">-->
          <div class="image plan-img" />
          <span>我的计划</span>
        </li>
        <li class="wrong" @click="$router.push('/study/wrong-questions')">
          <!--<img src="@/assets/images/danju-cuowu-tianchong@2x.png" alt="我的错题">-->
          <div class="image wrong-img" />
          <span>我的错题</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { Notification } from 'element-ui'
import _ from 'lodash'
export default {
  name: 'App',
  data() {
    return {
      shortcutImg: './assets/images/kuaijiejian-@2x.png',
      buildId: process.env.VUE_APP_BUILD_ID
    }
  },
  computed: {
    ...mapGetters([
      'getCurrentIMInfoMessages',
      'notify',
      'sessionId',
      'activedBabyId',
      'msgQueue',
      'bus_role',
      'newSingleAsk'
    ])
  },
  watch: {
    notify(val) {
      // 有新的咨询消息时，全局通知
      if (val) {
        this.showNotify(val)
      }
    },
    newSingleAsk(val) {
      // 有新的问诊消息时，全局通知
      if (val && val.data) {
        const data = JSON.parse(val.data)
        data.desc = val.desc
        this.showSingleAskNotify(data)
      }
    }
  },
  created() {},
  methods: {
    showSingleAskNotify(message) {
      const h = this.$createElement
      let tip =
        message.user_name + '向您发起了新的单次问诊，请在24小时内进行回复'
      if (message.status === 'normal') {
        Notification({
          iconClass: 'el-icon-bell my-bell',
          title: '新的待接诊问题',
          dangerouslyUseHTMLString: true,
          duration: 0,
          message: h('div', { class: 'clearfix' }, [
            h(
              'p',
              {
                class: 'title'
              },
              tip
            ),
            h(
              'button',
              {
                class: 'detail_btn',
                attrs: {
                  questionId: message.question,
                  status: 'normal',
                  userId: message.user_id
                },
                on: {
                  click: this.showSingAskDetail.bind(this)
                }
              },
              '查看详情'
            )
          ])
        })
      } else if (
        message.status === 'second_ask' ||
        message.status === 'first_ask' ||
        message.status === 'three_ask' ||
        message.status === 'third_ask'
      ) {
        tip = message.user_name + '向您发起了新的追问，请及时回复。'
        Notification.info({
          title: '新的追问',
          dangerouslyUseHTMLString: true,
          duration: 0,
          message: h('div', { class: 'clearfix' }, [
            h(
              'p',
              {
                class: 'title'
              },
              tip
            ),
            h(
              'button',
              {
                class: 'detail_btn',
                attrs: {
                  questionId: message.question,
                  status: 'reply',
                  userId: message.user_id
                },
                on: {
                  click: this.showSingAskDetail.bind(this)
                }
              },
              '查看详情'
            )
          ])
        })
      }
    },
    showSingAskDetail(fdaskid) {
      const target = fdaskid.target
      const questionId = target.getAttribute('questionId')
      const userId = target.getAttribute('userId')
      const status = target.getAttribute('status')
      const routePath = this.$route.path
      const singleSelect = {
        userId: userId,
        questionId: questionId,
        status: status
      }
      if (routePath.indexOf('consultation/detail') === -1) {
        this.$router.push({
          path: '/consultation/detail',
          query: {
            type: status,
            questionId: questionId,
            userId: userId,
            info: {}
          }
        })
      }
      this.$store.dispatch('ask/singleSelect', singleSelect, { root: true }) // 设置查看选中的用户及问题id
    },
    showNotify(message) {
      if (message.duration !== 0 && !message.isSelfSend) {
        // 当前发送消息的用户正在咨询中，不再转诊中，则此消息只推送给健康管理师
        const messageSessionInd = _.findIndex(this.msgQueue, function(o) {
          return o.last_ask.family_member === message.mymessage.family_member
        })
        // 接诊台用户列表中，存在当前发送消息的用户
        if (messageSessionInd !== -1) {
          // 当前用户的咨询状态不为转诊中且当前登录用户角色为hm 或者
          // 当前接收消息的用户咨询状态为转诊中且当前登录的用户的角色和转诊的用户角色一致，则推送新消息给当前登录的用户
          if (
            (!this.msgQueue[messageSessionInd].last_ask.has_transfer &&
              this.bus_role === 'hm') ||
            (this.msgQueue[messageSessionInd].last_ask.has_transfer &&
              this.bus_role ===
                this.msgQueue[messageSessionInd].last_ask.transfer_role)
          ) {
            this.$notify.info({
              title: '新消息提示',
              message:
                message.msgOptions.type === 'text'
                  ? message.msgOptions.text
                  : message.msgTextPrefix,
              duration: message.duration
            })
            this.msgAudio = new Audio()
            this.msgAudio.src =
              'https://pic1.baobaohehu.com/static/mp-ask/new-msg.mp3'
            this.msgAudio.volume = 0.3
            this.msgAudio.play()
            return
          }
        }
      }
      if (message.duration !== 0) {
        return
      }
      const h = this.$createElement
      let tip =
        '您的转诊请求已经被' +
        message.message_body.user.name +
        '拒绝，如有必要，请与' +
        message.message_body.user.name +
        '沟通后再次发起转诊请求'
      const notifyApplyMsgId = window.localStorage.getItem('notifyApplyMsgId')
      if (
        !notifyApplyMsgId ||
        notifyApplyMsgId !== message.message_body.message_id
      ) {
        window.localStorage.setItem(
          'notifyApplyMsgId',
          message.message_body.message_id
        )
        if (message.message_body.event_type === 'transfer_apply') {
          tip =
            message.message_body.user.name +
            '发起转诊请求' +
            '请及时处理，5分钟内未响应，系统将自动同意'
          Notification({
            iconClass: 'el-icon-bell my-bell',
            title: '转诊申请',
            dangerouslyUseHTMLString: true,
            duration: 0,
            message: h('div', { class: 'clearfix' }, [
              h(
                'p',
                {
                  class: 'title'
                },
                tip
              ),
              h(
                'button',
                {
                  class: 'detail_btn',
                  attrs: {
                    fd_ask: message.message_body.fd_ask,
                    message_id: message.message_body.message_id,
                    fd_session: message.message_body.fd_session,
                    family_member: message.message_body.family_member
                  },
                  on: {
                    click: this.showMsg.bind(this)
                  }
                },
                '查看详情'
              )
            ])
          })
        } else if (message.message_body.event_type === 'transfer_accept') {
          tip =
            '您的转诊请求已经被' +
            message.message_body.user.name +
            '同意，请与用户确认后请单击【发起转诊】按钮'
          Notification.success({
            title: '转诊请求被同意',
            dangerouslyUseHTMLString: true,
            duration: 0,
            message: h('div', { class: 'clearfix' }, [
              h(
                'p',
                {
                  class: 'title'
                },
                tip
              ),
              h(
                'button',
                {
                  class: 'detail_btn',
                  attrs: {
                    fd_ask: message.message_body.fd_ask,
                    message_id: message.message_body.message_id,
                    fd_session: message.message_body.fd_session,
                    family_member: message.message_body.family_member
                  },
                  on: {
                    click: this.showMsg.bind(this)
                  }
                },
                '查看详情'
              )
            ])
          })
        } else if (message.message_body.event_type === 'transfer_reject') {
          Notification.error({
            title: '转诊请求被拒绝',
            dangerouslyUseHTMLString: true,
            duration: 0,
            message: h('div', { class: 'clearfix' }, [
              h(
                'p',
                {
                  class: 'title'
                },
                tip
              ),
              h(
                'button',
                {
                  class: 'detail_btn',
                  attrs: {
                    fd_ask: message.message_body.fd_ask,
                    message_id: message.message_body.message_id,
                    fd_session: message.message_body.fd_session,
                    family_member: message.message_body.family_member
                  },
                  on: {
                    click: this.showMsg.bind(this)
                  }
                },
                '查看详情'
              )
            ])
          })
        }
      }
    },
    async showMsg(fdaskid) {
      const routePath = this.$route.path
      const target = fdaskid.target
      const message_id = target.getAttribute('message_id')
      const fd_session = target.getAttribute('fd_session')
      const family_member = target.getAttribute('family_member')
      const currentObj = await this.$store.dispatch(
        'getSessionObjById',
        fd_session
      )
      const obj = {
        session_id: fd_session,
        userId: currentObj.user._id,
        babyId: currentObj.family_member ? currentObj.family_member._id : ''
      }
      if (routePath.indexOf('diagnose') === -1) {
        this.$router.push({
          path: '/medical-service/diagnose',
          query: {
            messageId: message_id,
            sessionId: fd_session,
            userId: obj.userId,
            babyId: obj.babyId
          }
        })
        return
      }
      // 跳转到当前消息的位置 message_id
      if (
        this.sessionId &&
        this.sessionId === fd_session &&
        this.activedBabyId &&
        family_member === this.activedBabyId
      ) {
        this.$store.dispatch('goMessageDetail', message_id)
      } else {
        await this.$store.dispatch('setCustomerInfo', obj)
        this.$store.dispatch('goMessageDetail', message_id)
      }
    }
  }
}
</script>
<style scoped lang="scss">
.build-id {
  position: fixed;
  right: 5px;
  bottom: 5px;
  font-size: 12px;
  font-weight: bold;
  color: #999;
  z-index: 100;
}
.shortcut-menu {
  position: fixed;
  top: 50%;
  right: -70px;
  margin-top: -39px;
  width: 78px;
  height: 222px;
  background: #fff;
  border-radius: 15px 0 0 15px;
  z-index: 999;
  box-shadow: 0 2px 12px 0 rgba(153, 153, 153, 0.3);
  transition: right 0.3s ease;
  &:hover {
    right: 0;
  }
  .list {
    font-size: 14px;
    text-align: center;
    color: #17233d;
    .keys,
    .plan,
    .wrong {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 74px;
      flex-direction: column;
      cursor: pointer;
      .image {
        margin: 5px 0;
        width: 20px;
        height: 20px;
        background-size: 100% 100%;
        &.shortcut-img {
          background-image: url('./assets/images/kuaijiejian-@2x.png');
        }
        &.plan-img {
          background-image: url('./assets/images/icon_dingtab1@2x.png');
        }
        &.wrong-img {
          background-image: url('./assets/images/danju-cuowu-tianchong@2x.png');
        }
      }
      &:hover {
        color: #0085ff;
        .shortcut-img {
          background-image: url('./assets/images/kuaijiejian-1@2x.png');
        }
        .plan-img {
          background-image: url('./assets/images/icon_dingtab@2x.png');
        }
        .wrong-img {
          background-image: url('./assets/images/Shape1@2x.png');
        }
      }
    }
    .keys,
    .plan {
      border: 1px solid #ebeef5;
    }
  }
  .news {
    /deep/.el-badge__content {
      top: -3px;
      right: 8px;
    }
  }
}
</style>
