import Layout from '@/layout'
import Parent from '@/components/Parent/index.vue'
const perfix_rid = 'obstetricsDoctor'
const goLocalName = window.location.origin

export default {
  r_id: perfix_rid + 1001,
  path: '/obstetricsDoctor',
  component: Layout,
  redirect: '/obstetricsDoctor/health/follow',
  alwaysShow: true,
  name: 'obstetricsDoctor',
  meta: {
    title: '产科家庭医生服务',
    icon: 'chanke_family_doctor',
    name: 'obstetricsDoctor'
  },
  children: [
    {
      r_id: perfix_rid + 1002,
      path: goLocalName + '/chanke/medical-service/diagnose',
      meta: {
        title: '产科接诊台',
        icon: 'jzt'
      },
      children: []
    },
    {
      r_id: perfix_rid + 1003,
      path: 'health/follow',
      component: { render: h => h('router-view') },
      meta: {
        title: '产科健康随访',
        name: 'owner',
        icon: 'chanke_health_follow'
      },
      children: [
        {
          r_id: perfix_rid + 100301,
          path: '',
          hidden: true,
          component: () =>
            import('@/views/obstetrics-doctor/healthFollow/index.vue'),
          meta: {
            name: 'ChankeFollow',
            breadcrumb: false,
            activeMenu: '/obstetricsDoctor/health/follow',
            keepAlive: true
          }
        },
        {
          r_id: perfix_rid + 10002.32,
          path: 'add',
          hidden: true,
          name: 'ChankeFollowEdit',
          component: () => import('@/views/obstetrics-doctor/healthFollow/add'),
          meta: {
            title: '新建健康随访任务',
            breadcrumb: true,
            icon: 'chanke_health_follow',
            activeMenu: '/obstetricsDoctor/health/follow'
          }
        },
        {
          r_id: perfix_rid + 10002.33,
          path: 'send',
          hidden: true,
          name: 'ChankeFollowSend',
          component: () =>
            import('@/views/obstetrics-doctor/healthFollow/send'),
          meta: {
            title: '发送随访消息',
            breadcrumb: true,
            icon: 'chanke_health_follow',
            activeMenu: '/obstetricsDoctor/health/follow'
          }
        },
        {
          r_id: perfix_rid + 10002.34,
          path: 'detail',
          hidden: true,
          name: 'ChankeFollowDetail',
          component: () =>
            import('@/views/obstetrics-doctor/healthFollow/detail'),
          meta: {
            title: '查看详情',
            breadcrumb: true,
            icon: 'tree',
            activeMenu: '/obstetricsDoctor/health/follow'
          }
        },
        {
          r_id: perfix_rid + 10002.35,
          path: 'comment',
          hidden: true,
          name: 'ChankeFollowComment',
          component: () =>
            import('@/views/obstetrics-doctor/healthFollow/comment'),
          meta: {
            title: '留言待回复',
            breadcrumb: true,
            icon: 'tree',
            activeMenu: '/obstetricsDoctor/health/follow'
          }
        },
        {
          r_id: perfix_rid + 10002.36,
          path: 'close',
          hidden: true,
          name: 'ChankeFollowCloseDetail',
          component: () =>
            import('@/views/obstetrics-doctor/healthFollow/close'),
          meta: {
            title: '查看详情',
            breadcrumb: true,
            activeMenu: '/obstetricsDoctor/health/follow'
          }
        }
      ]
    },
    {
      r_id: perfix_rid + 1004,
      path: 'healthFile',
      component: Parent,
      meta: {
        title: '产科健康档案',
        name: 'obstetricsDoctorTable',
        activeMenu: '/obstetricsDoctor/healthFile',
        icon: 'chanke_health_file'
      },
      children: [
        {
          r_id: perfix_rid + 100401,
          path: '',
          hidden: true,
          component: () =>
            import('@/views/obstetrics-doctor/healthFile/index.vue'),
          meta: {
            name: 'obstetricsDoctor',
            breadcrumb: false,
            activeMenu: '/obstetricsDoctor/healthFile',
            keepAlive: true
          }
        },
        {
          r_id: perfix_rid + 100402,
          path: 'detail/:id',
          hidden: true,
          component: () =>
            import('@/views/obstetrics-doctor/healthFile/detail/index.vue'),
          meta: {
            name: 'obstetricsDoctor',
            title: '档案详情',
            breadcrumb: true,
            activeMenu: '/obstetricsDoctor/healthFile'
          }
        }
      ]
    }
  ]
}
