// 回合制相关接口
import request from '@/utils/request'

// 获取消息队列
export function getMsgQueueApi(params) {
  return request({
    url: `/fd/health_ask/chat_list`,
    method: 'get',
    params: params
  })
}

// 新增回合
export function addBoutApi(id, params) {
  return request({
    url: `fd/health_ask/ask/${id}/round/apply_append`,
    method: 'put',
    data: params
  })
}
// 回合制结束咨询
export function closeBoutConsultApi(id) {
  return request({
    url: `fd/health_ask/ask/${id}/round/append_end`,
    method: 'put'
  })
}
// 用户转诊意向确认
export function confirmUserIntent(msgId, params) {
  return request({
    url: `fd/health_ask/message/${msgId}/handler_apply_transfer`,
    method: 'put',
    data: params
  })
}
// 健康管理师确认收到医生同意转诊的通知
export function confrimAccept(messageId, params) {
  return request({
    url: `/fd/health_ask/message/${messageId}/todo`,
    method: 'put',
    data: params
  })
}
//获取last_ask的信息
export function getLastAsk(sessionId) {
  return request({
    url: `/fd/health_ask/chat/${sessionId}/ask_info`,
    method: 'get'
  })
}
