import Vue from 'vue'
import Vuex from 'vuex'
import getters from './getters'
import app from './modules/app'
import settings from './modules/settings'
import permission from './modules/permission'
import user from './modules/user'
import study from './modules/study'
import im from './modules/im'
import myIM from './modules/myIM'
import chat from './modules/chat'
import diagnose from './modules/diagnose'
import health from './modules/health'
import recipes from './modules/recipes'
import babyExtend from './modules/babyExtend'
import ask from './modules/ask'
import crm from './modules/crm'
import msgQueue from './modules/msgQueue'
import dictionary from './modules/dictionary'
import obstetrics from './modules/obstetrics'
import peopleByRole from './modules/peopleByRole'
Vue.use(Vuex)

const store = new Vuex.Store({
  modules: {
    app,
    settings,
    user,
    permission,
    study,
    im,
    myIM,
    chat,
    diagnose,
    health,
    recipes,
    babyExtend,
    crm,
    ask,
    msgQueue,
    dictionary,
    obstetrics,
    peopleByRole
  },
  getters
})

export default store
