import { asyncRoutes, constantRoutes } from '@/router'
import { getUserRoutes } from '@/api/role'
import _ from 'lodash'

function generateTree(routes, checkedKeys) {
  for (const route of routes) {
    // const routePath = path.resolve(basePath, route.path)

    // recursive child routes
    if (route.children && route.children.length) {
      route.children = generateTree(route.children, checkedKeys)
    }

    if (!checkedKeys.includes(route.r_id)) {
      // res.push(route)
      route.hidden = true
    }
  }
  return routes
}

function setDefaultRedirect(routes = []) {
  for (let i = 0; i < routes.length; i++) {
    const route = routes[i]
    if (!route.hidden && route.children && route.children.length) {
      const unhide = route.children.find(item => {
        return !item.hidden
      })
      if (unhide && unhide.children) {
        setDefaultRedirect(unhide.children)
      } else if (unhide) {
        route.redirect = unhide.path.replace(window.location.origin, '')
      }
    }
  }
}

const state = {
  routes: [],
  addRoutes: []
}

const mutations = {
  SET_ROUTES: (state, routes) => {
    state.addRoutes = routes
    state.routes = constantRoutes.concat(routes)
  },
  SET_ALLROUTES: (state, routes) => {
    state.allRoutes = routes
  }
}

const actions = {
  getUserRoutes({ commit, rootGetters }) {
    // 深度拷贝asyncRoutes
    const allRoutes = _.cloneDeep(asyncRoutes)
    return new Promise((resolve, reject) => {
      getUserRoutes()
        .then(({ data }) => {
          let accessedRoutes

          if (!rootGetters.roles.includes('superAdmin')) {
            accessedRoutes = generateTree(allRoutes, data)
            setDefaultRedirect(accessedRoutes)
          } else {
            accessedRoutes = asyncRoutes
          }
          commit('SET_ROUTES', accessedRoutes)
          resolve(accessedRoutes)
        })
        .catch(error => {
          reject(error)
        })
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
