import request from '@/utils/request'
// 编辑咨询建议
export function saveSuggestion(params) {
  return request({
    url: `/fd/health_ask/ask/${params.id}/change_ask_suggestion`,
    method: 'put',
    data: params
  })
}
export function getAllHistory(params) {
  return request({
    url: 'fd/health_ask/asks',
    method: 'get',
    params: params
  })
}
export function getRecentHistory(params) {
  return request({
    url: '/fd/health_ask/ask_mixs',
    method: 'get',
    params: params
  })
}
export function getTimeLineInfo(id) {
  return request({
    url: `/fd/health_ask/ask/${id}/path_info`,
    method: 'get'
  })
}

export function getCustomerTree() {
  return request({
    url: '/fd/health_ask/user_list',
    method: 'get'
  })
}

export function getMockChatByBabySession(id) {
  return request({
    url: `/fd/health_ask/chat/${id}/mock_chat`,
    method: 'get'
  })
}
