import Layout from '@/layout'

const perfix_rid = 'consultation'
const goLocalName = window.location.origin

export default {
  r_id: perfix_rid + 1001,
  path: '/consultation',
  component: Layout,
  redirect: '/consultation/list',
  alwaysShow: true,
  name: 'consultation',
  meta: {
    title: '问诊',
    icon: 'wenzhen'
  },
  children: [
    {
      r_id: perfix_rid + 1002,
      path: 'list',
      component: () => import('@/views/consultation'),
      meta: {
        title: '问诊管理',
        icon: 'wenzhenguanli'
      },
      children: [
        {
          r_id: perfix_rid + 1003,
          path: '',
          hidden: true,
          name: 'ConsultationList',
          component: () => import('@/views/consultation/list.vue'),
          meta: {
            title: '问诊列表',
            breadcrumb: false,
            activeMenu: '/consultation/list'
          }
        }
      ]
    },
    {
      r_id: perfix_rid + 1004,
      path: `${goLocalName}/consultation/detail`,
      meta: {
        title: '问诊详情',
        breadcrumb: true,
        activeMenu: '/consultation/list',
        icon: 'wenzhenxiangqing'
      }
    },
    {
      r_id: perfix_rid + 1005,
      path: 'professional-role',
      component: () => import('@/views/system-settings/professional-role'),
      meta: {
        title: '业务角色',
        icon: 'namecard-fill'
      },
      children: [
        {
          r_id: perfix_rid + 1006,
          path: '',
          hidden: true,
          name: 'singleAskRole',
          query: {
            pageType: 'consultation'
          },
          component: () =>
            import('@/views/system-settings/professional-role/main'),
          meta: {
            title: '业务角色',
            breadcrumb: false,
            activeMenu: '/consultation/professional-role'
          }
        },
        {
          r_id: perfix_rid + 1007,
          path: ':id/edit/:role',
          hidden: true,
          name: 'singleAskRoleEdit',
          component: () =>
            import('@/views/system-settings/professional-role/edit'),
          meta: {
            title: '新增成员',
            activeMenu: '/consultation/professional-role'
          }
        }
      ]
    }
  ]
}
