import request from '@/utils/request'

export function getLoginInfo() {
  return request({
    url: '/user/logininfo'
  })
}

export function login(data) {
  return request({
    url: '/user/login',
    method: 'post',
    data
  })
}

export function getInfo() {
  return request({
    url: '/user/userinfo',
    method: 'get'
  })
}

export function logout() {
  return request({
    url: '/user/logout',
    method: 'post'
  })
}
export function getUserHeart() {
  return request({
    url: '/user/heart_beat',
    method: 'get'
  })
}
