import request from '@/utils/request'

export function getRoutes() {
  return request({
    url: '/fontend_routes',
    method: 'get'
  })
}

export function sendRoutes(data) {
  return request({
    url: '/fontend_routes',
    method: 'post',
    data
  })
}

export function getUserRoutes() {
  return request({
    url: '/user/fontend_routes',
    method: 'get'
  })
}

export function getApiAccessTree() {
  return request({
    url: '/role/access',
    method: 'get'
  })
}

export function getRoles(params) {
  return request({
    url: '/role/list',
    method: 'get',
    params
  })
}

export function updateRoleBaseInfo(id, data) {
  return request({
    url: '/role' + `/${id}`,
    method: 'put',
    data
  })
}

export function addRole(data) {
  return request({
    url: '/role',
    method: 'post',
    data
  })
}

export function updateRoleRoutes(id, data) {
  return request({
    url: '/role' + `/${id}/fontend_access`,
    method: 'put',
    data
  })
}

export function updateRoleBackendAccess(id, data) {
  return request({
    url: '/role' + `/${id}/backend_access`,
    method: 'put',
    data
  })
}

export function deleteRole(id) {
  return request({
    url: '/role' + `/${id}`,
    method: 'delete'
  })
}
