const diagnose = {
  state: {
    // 转诊状态
    diagnose_statue: '',
    notify: {},
    replyBoxFlag: false
  },
  mutations: {
    // 设置转诊状态
    SET_DIAGNOSE_STATUS: (state, payload) => {
      state.diagnose_statue = payload
    },
    SET_NOTIFY: (state, msg) => {
      state.notify = msg
    },
    SET_REPLY_BOX_STATUS: (state, flag) => {
      state.replyBoxFlag = flag
    }
  },
  actions: {
    notify({ commit }, message) {
      commit('SET_NOTIFY', message)
    },
    setReplyBoxFlag({ commit }, flag) {
      commit('SET_REPLY_BOX_STATUS', flag)
    }
  }
}

export default diagnose
