import Layout from '@/layout'
import Parent from '@/components/Parent/index.vue'

const perfix_rid = 'user-manage'

export default {
  r_id: perfix_rid + 10001,
  path: '/user-manage',
  component: Layout,
  meta: {
    title: '用户管理',
    icon: 'user-manage',
    activeMenu: '/user-manage'
  },
  children: [
    {
      r_id: perfix_rid + 10002,
      path: '',
      name: 'userManage',
      component: () => import('@/views/user-management/index'),
      meta: {
        title: '用户管理',
        icon: 'user-manage',
        activeMenu: '/user-manage'
      }
    },
    {
      hidden: true,
      r_id: perfix_rid + 10003,
      path: 'user-info/:id',
      name: 'userInfo',
      component: () => import('@/views/user-management/userInfo'),
      meta: {
        title: '用户信息详情',
        icon: 'new-recruit-fil',
        activeMenu: '/user-manage'
      }
    },
    {
      r_id: perfix_rid + 10005,
      path: 'customer-type',
      hidden: false,
      component: () => import('@/views/system-settings/customer-type/index'),
      meta: {
        title: '用户类型管理',
        icon: 'user-type'
      }
    },
    {
      hidden: false,
      r_id: perfix_rid + 10202,
      path: 'assistant',
      name: 'assistant',
      component: () => import('@/views/assistant-management/index'),
      meta: {
        title: '小助手管理',
        icon: 'xiaozhushou'
      }
    },
    {
      r_id: perfix_rid + 10008,
      path: 'programe-code',
      hidden: false,
      component: Parent,
      meta: {
        title: '小程序码管理',
        icon: 'program-code'
      },
      children: [
        {
          r_id: perfix_rid + 10008.1,
          path: '',
          name: 'programeCode',
          hidden: true,
          component: () => import('@/views/user-management/program-code/index'),
          meta: {
            title: '小程序码管理',
            breadcrumb: false
          }
        },
        {
          r_id: perfix_rid + 10008.2,
          path: 'add-code',
          name: 'addCode',
          hidden: true,
          component: () =>
            import('@/views/user-management/program-code/AddCode'),
          meta: {
            title: '新增小程序码',
            activeMenu: '/user-manage/programe-code'
          }
        }
      ]
    },
    {
      r_id: perfix_rid + 10004,
      path: 'source-channel',
      hidden: false,
      component: () => import('@/views/system-settings/source-channel/index'),
      meta: {
        title: '来源渠道管理',
        icon: 'user-sence'
      }
    },
    {
      r_id: perfix_rid + 10006,
      path: 'follow-operation',
      hidden: false,
      component: () => import('@/views/system-settings/follow-operation/index'),
      meta: {
        title: '跟进操作管理',
        icon: 'follow-action'
      }
    },
    {
      r_id: perfix_rid + 10007,
      path: 'channel-scene',
      hidden: false,
      component: Parent,
      meta: {
        title: '渠道场景管理',
        icon: 'changjing'
      },
      children: [
        {
          r_id: perfix_rid + 10007.1,
          path: '',
          name: 'channelScene',
          hidden: true,
          component: () =>
            import('@/views/system-settings/channel-source/index'),
          meta: {
            title: '渠道场景管理',
            breadcrumb: false
          }
        },
        {
          r_id: perfix_rid + 10007.2,
          path: 'add-source',
          name: 'addSource',
          hidden: true,
          component: () =>
            import('@/views/system-settings/channel-source/AddSource'),
          meta: {
            title: '新增场景',
            activeMenu: '/user-manage/channel-scene'
          }
        },
        {
          r_id: perfix_rid + 10007.3,
          path: 'edit-source/:id',
          name: 'editSource',
          hidden: true,
          component: () =>
            import('@/views/system-settings/channel-source/AddSource'),
          meta: {
            title: '编辑场景',
            activeMenu: '/user-manage/channel-scene'
          }
        }
      ]
    }
  ]
}
