import {
  login,
  logout,
  getInfo,
  foreverToken,
  setForeverToken
} from '@/api/user'
import { getToken, setToken, removeToken } from '@/utils/auth'
import { resetRouter } from '@/router'

const state = {
  token: getToken(),
  userInfo: {},
  name: '',
  avatar: '',
  roles: [],
  mts_user: {},
  habit: {},
  id: '',
  im: '',
  transferRole: '',
  bus_role: '',
  question_online_status: '',
  myReplaceStatus: {}
}

const mutations = {
  SET_HABIT: (state, habit) => {
    state.habit = habit
  },
  SET_REPLACE: (state, status) => {
    state.myReplaceStatus = status
  },
  SET_USERINFO: (state, info) => {
    state.userInfo = info
    state.userInfo.id = info._id
  },
  SET_TOKEN: (state, token) => {
    state.token = token
  },
  SET_NAME: (state, name) => {
    state.name = name
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar
  },
  SET_ROLES: (state, roles) => {
    state.roles = roles
  },
  SET_TYPE: (state, mts_user) => {
    state.mts_user = mts_user
  },
  SET_ID: (state, id) => {
    state.id = id
  },
  SET_BUS_ROLE: (state, bus_role) => {
    state.bus_role = bus_role
  },
  SET_TRANSFER_ROLE: (state, val) => {
    state.transferRole = val
  },
  SET_IM: (state, im) => {
    state.im = im
  },
  SET_STATUS: (state, status) => {
    state.question_online_status = status
  }
}
const actions = {
  // user login
  login({ commit }, userInfo) {
    const { code } = userInfo
    return new Promise((resolve, reject) => {
      login({ tmp_auth_code: code })
        .then(response => {
          const { data } = response
          commit('SET_TOKEN', data.token)
          setToken(data.token)
          resolve()
        })
        .catch(error => {
          reject(error)
        })
    })
  },
  setReplace({ commit }, status) {
    commit('SET_REPLACE', status)
  },
  // get user info
  getInfo({ commit, dispatch, state }) {
    return new Promise((resolve, reject) => {
      getInfo()
        .then(response => {
          const { data } = response

          if (!data) {
            reject('Verification failed, please Login again.')
          }

          const {
            roles,
            name,
            avatar,
            mts_user,
            // id,
            _id,
            bus_role,
            im,
            user_habit,
            question_online_status
          } = data

          // roles must be a non-empty array
          if (!roles || roles.length <= 0) {
            reject('getInfo: roles must be a non-null array!')
          }
          commit('SET_ROLES', roles)
          commit('SET_NAME', name)
          commit('SET_AVATAR', avatar)
          commit('SET_TYPE', mts_user)
          commit('SET_ID', _id)
          commit('SET_IM', im)
          commit('SET_HABIT', user_habit)
          commit('SET_STATUS', question_online_status)
          commit('SET_USERINFO', data)
          commit('im/updateLoginInfo', im, { root: true })
          dispatch('im/loginAsync', '', { root: true })
          commit('SET_BUS_ROLE', bus_role)
          // TODOS: 产科医生角色转换
          let role = ''
          if (
            bus_role === 'doctor' &&
            data.fd_info.department === 'obstetrics'
          ) {
            role = 'hm'
          } else {
            role = bus_role
          }
          commit('SET_TRANSFER_ROLE', role)
          resolve(data)
        })
        .catch(error => {
          reject(error)
        })
    })
  },

  // user logout
  logout({ commit, dispatch, state }) {
    return new Promise((resolve, reject) => {
      logout()
        .then(() => {
          commit('SET_ROLES', [])
          commit('SET_TOKEN', '')
          commit('SET_NAME', '')
          commit('SET_AVATAR', '')
          removeToken()
          resetRouter()
          window.sessionStorage.clear()
          window.localStorage.clear()
          dispatch('im/loginOut', '', { root: true })
          resolve()
        })
        .catch(error => {
          reject(error)
        })
    })
  },

  // remove token
  resetToken({ commit }) {
    return new Promise(resolve => {
      commit('SET_TOKEN', '')
      commit('SET_ROLES', [])
      commit('SET_NAME', '')
      commit('SET_AVATAR', '')
      removeToken()
      resolve()
    })
  },

  // foreverToken
  foreverToken({ commit }) {
    return new Promise((resolve, reject) => {
      foreverToken()
        .then(response => {
          const { data } = response
          commit('SET_TOKEN', data.token)
          setToken(data.token)
          resolve()
        })
        .catch(error => {
          reject(error)
        })
    })
  },
  setForeverToken({ commit }, userInfo) {
    return new Promise((resolve, reject) => {
      setForeverToken(userInfo)
        .then(response => {
          const { data } = response
          commit('SET_TOKEN', data.token)
          setToken(data.token)
          resolve(data)
        })
        .catch(error => {
          reject(error)
        })
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
