<template>
  <div :class="classObj" class="app-wrapper">
    <app-header />
    <div class="app-wrapper__body">
      <div
        v-if="device === 'mobile' && sidebar.opened"
        class="drawer-bg"
        @click="handleClickOutside"
      />
      <sidebar class="sidebar-container" />
      <div class="main-container">
        <div :class="{ 'fixed-header': fixedHeader }">
          <navbar />
        </div>
        <app-main />
      </div>
    </div>
    <tip-list
      :notification-list="notificationList"
      @obtainNotification="obtainNotification"
    />
    <div v-if="isMinimize" class="message-box">
      <div class="tip-text">
        <el-badge :value="notificationList.length" class="news">
          <p class="text">您有新消息待处理</p>
        </el-badge>
      </div>
      <div style="position: absolute;top: 130px;width: 100%;height: 2px;" />
      <div class="info-detail">
        <el-badge
          v-if="getTipNum('-1').length"
          :value="getTipNum('-1').length"
          class="work-shift"
          @click.native="trialPost('-1')"
        >
          <p>试岗阶段待处理</p>
        </el-badge>
        <template v-for="(item, index) of 6">
          <el-badge
            v-if="getTipNum(`${index}`).length"
            :key="index"
            :value="getTipNum(`${index}`).length"
            class="work-shift"
            @click.native="trialPost(index + '')"
          >
            <p>第{{ getStage(`${index}`) }}阶段待处理</p>
          </el-badge>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { Navbar, Sidebar, AppMain, AppHeader } from './components'
import ResizeMixin from './mixin/ResizeHandler'
import TipList from '@/components/TipList'
import { obtainNotification, minimize } from '@/api/learningAdmin'
export default {
  name: 'Layout',
  components: {
    Navbar,
    Sidebar,
    AppMain,
    TipList,
    AppHeader
  },
  mixins: [ResizeMixin],
  data() {
    return {
      notificationList: [],
      tipNum: 0
    }
  },
  computed: {
    sidebar() {
      return this.$store.state.app.sidebar
    },
    device() {
      return this.$store.state.app.device
    },
    fixedHeader() {
      return this.$store.state.settings.fixedHeader
    },
    classObj() {
      return {
        hideSidebar: !this.sidebar.opened,
        openSidebar: this.sidebar.opened,
        withoutAnimation: this.sidebar.withoutAnimation,
        mobile: this.device === 'mobile'
      }
    },
    isMinimize() {
      return this.notificationList.filter(v => v.status === 'small').length
    },
    getTipNum() {
      return function(type) {
        return this.notificationList.filter(v => v.type === type)
      }
    },
    getStage() {
      return function(stage) {
        if (stage === '0') return '一'
        if (stage === '1') return '二'
        if (stage === '2') return '三'
        if (stage === '3') return '四'
        if (stage === '4') return '五'
        if (stage === '5') return '六'
      }
    }
  },
  async mounted() {
    const self = this
    setTimeout(function() {
      self.positionTip()
    }, 200)
    if (this.$store.state.user.mts_user) {
      const type = this.$store.state.user.mts_user.type
      if (
        type === 'teacher' ||
        type === 'doctor' ||
        type === 'human_resource'
      ) {
        this.obtainNotification()
      }
    }
  },
  methods: {
    handleClickOutside() {
      this.$store.dispatch('app/closeSideBar', { withoutAnimation: false })
    },
    async obtainNotification() {
      const { data, code } = await obtainNotification()
      if (code === 20000) {
        if (data.result && data.list.length) {
          this.notificationList = []
          const tipOne = data.list.find(item => {
            return item.type === '-1'
          })
          const tipTwo = data.list.find(item => {
            return item.type === '-2'
          })
          const tipThree = data.list.find(item => {
            return item.type !== '-2' && item.type !== '-1'
          })
          if (tipThree) this.notificationList.push(tipThree)
          if (tipOne) this.notificationList.push(tipOne)
          if (tipTwo) this.notificationList.push(tipTwo)
        }
      }
    },
    positionTip() {
      // 提示框定位
      const mts = document.getElementsByClassName('message-tips')
      if (mts) {
        for (let i = 0; i < mts.length; i++) {
          mts[i].style.right = '16px'
          mts[i].style.top = i * mts[i].offsetHeight + 16 * (i + 1) + 'px'
        }
      }
    },
    async trialPost(stage) {
      const { code, data } = await minimize({
        stage: stage
      })
      if (code === 20000 && data.result) {
        await this.obtainNotification()
        const self = this
        setTimeout(function() {
          self.positionTip()
        }, 200)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';
@import '~vue2-editor/dist/vue2-editor.css';
@import '~quill/dist/quill.core.css';
@import '~quill/dist/quill.bubble.css';
@import '~quill/dist/quill.snow.css';

.app-wrapper {
  width: 100vw;
  height: 100vh;
  &__body {
    @include clearfix;

    position: relative;
    padding: 0 20px 20px 20px;
    height: calc(100vh - #{$headerHei});
    background-color: #eee;
    &.mobile.openSidebar {
      position: fixed;
      top: 0;
    }
  }
}
.drawer-bg {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.3;
  z-index: 999;
}

.fixed-header {
  position: fixed;
  top: 0;
  right: 0;
  width: calc(100% - #{$sideBarWidth});
  z-index: 9;
  transition: width 0.28s;
}

.hideSidebar .fixed-header {
  width: calc(100% - 54px);
}

.mobile .fixed-header {
  width: 100%;
}

.message-box {
  position: fixed;
  right: 80px;
  bottom: 100px;
  width: 115px;
  height: 130px;
  font-size: 10px;
  color: #0085ff;
  background-size: 115px 130px;
  z-index: 999;
  transition: all 0.3s;
  background-image: url('https://pic1.baobaohehu.com/fd/admin_v3/5d7f5f5c1c03121b82179aa8//20200325111118599/1585105877655.png'); // ../assets/images/message_tip@2x.png
  cursor: pointer;

  .info-detail {
    position: absolute;
    top: 132px;
    overflow: hidden;
    padding: 0 5px;
    width: 100%;
    height: 0;
    background: #fdb1a4;
    box-sizing: border-box;
    /deep/.el-badge {
      width: 100%;
    }
    p {
      margin: 4px 0;
      text-align: center;
      &:first-child {
        margin-top: 8px;
      }
    }
    &::after {
      position: absolute;
      top: -5px;
      left: 10px;
      height: 2px;
      border: 2px solid #d86551;
      content: '';
    }
  }
  &:hover .info-detail {
    height: auto;
    border: 1px solid #d86551;
  }
  .tip-text {
    position: absolute;
    bottom: 10px;
    width: 100%;
    text-align: center;
    .news {
      /deep/.el-badge__content {
        top: -2px;
      }
    }
  }

  /deep/.el-badge__content.is-fixed {
    top: 5px;
    padding: 0 4px;
    height: 12px;
    line-height: 12px;
  }
}
</style>
