import Layout from '@/layout'
import Parent from '@/components/Parent/index.vue'
const perfix_rid = 'crm'

export default {
  r_id: perfix_rid + 1001,
  path: '/crm',
  component: Layout,
  redirect: '/crm/customer/store',
  name: 'crm',
  hidden: true,
  meta: {
    title: '客户管理',
    icon: 'crm_manage',
    name: 'crm',
    activeMenu: '/crm/customer/store'
  },
  children: [
    {
      r_id: perfix_rid + 1002,
      path: 'customer/store',
      component: Parent,
      meta: {
        title: '客户库',
        icon: 'crm_store',
        name: 'store',
        activeMenu: '/crm/customer/store',
        breadcrumb: true
      },
      children: [
        {
          r_id: perfix_rid + 100201,
          path: '',
          hidden: true,
          component: () => import('@/views/crm/customer/store/index.vue'),
          meta: {
            name: 'store',
            breadcrumb: false,
            activeMenu: '/crm/customer/store',
            keepAlive: true
          }
        },
        {
          r_id: perfix_rid + 100202,
          path: 'detail/:id',
          hidden: true,
          component: () => import('@/views/crm/customer/detail/index.vue'),
          meta: {
            name: 'store',
            title: '查看详情',
            breadcrumb: true,
            activeMenu: '/crm/customer/store'
          }
        },
        {
          r_id: perfix_rid + 100203,
          path: 'related',
          hidden: true,
          component: () => import('@/views/crm/customer/add/related'),
          meta: {
            name: 'store',
            title: '关联客户',
            breadcrumb: true,
            activeMenu: '/crm/customer/store'
          }
        },
        {
          r_id: perfix_rid + 100204,
          path: 'add',
          hidden: true,
          component: () => import('@/views/crm/customer/add'),
          meta: {
            name: 'store_info',
            title: '客户信息',
            breadcrumb: true,
            activeMenu: '/crm/customer/store'
          }
        }
      ]
    },
    {
      r_id: perfix_rid + 1003,
      path: 'customer/owner',
      component: Parent,
      meta: {
        title: '个人负责客户',
        name: 'owner',
        icon: 'crm_owner'
      },
      children: [
        {
          r_id: perfix_rid + 100301,
          path: '',
          hidden: true,
          component: () => import('@/views/crm/customer/owner/index.vue'),
          meta: {
            name: 'owner',
            breadcrumb: false,
            activeMenu: '/crm/customer/owner',
            keepAlive: true
          }
        },
        {
          r_id: perfix_rid + 100302,
          path: 'detail/:id',
          hidden: true,
          component: () => import('@/views/crm/customer/detail/index.vue'),
          meta: {
            name: 'owner',
            title: '查看详情',
            breadcrumb: true,
            activeMenu: '/crm/customer/owner'
          }
        },
        {
          r_id: perfix_rid + 100303,
          path: 'related',
          hidden: true,
          component: () => import('@/views/crm/customer/add/related'),
          meta: {
            name: 'owner',
            title: '关联客户',
            breadcrumb: true,
            activeMenu: '/crm/customer/owner'
          }
        },
        {
          r_id: perfix_rid + 100304,
          path: 'add',
          hidden: true,
          component: () => import('@/views/crm/customer/add'),
          meta: {
            name: 'owner_info',
            title: '客户信息',
            breadcrumb: true,
            activeMenu: '/crm/customer/owner'
          }
        }
      ]
    }
  ]
}
