import { getTemplateDic } from '@/api/recipes'
// import { getBabyList } from '@/api/recipes'

const state = {
  // babyList: [],
  taskCount: {},
  creater: [],
  sender: [],
  // selectBaby: null,
  templateDic: []
}
const mutations = {
  SET_TASKCOUNT: (state, countObj) => {
    state.taskCount = countObj
  },
  SET_CREATERS: (state, creaters) => {
    state.creater = creaters
  },
  SET_SENDERS: (state, senders) => {
    state.sender = senders
  },
  SET_TEMPLATEDIC: (state, templateDic) => {
    state.templateDic = templateDic
  }
}
const actions = {
  getTemplateDic: ({ commit }, params = { show_replace: 0 }) => {
    getTemplateDic(params).then(res => {
      commit('SET_TEMPLATEDIC', res.data.list)
    })
  }
}
export default {
  namespaced: true,
  state,
  mutations,
  actions
}
