import request from '@/utils/request'

export function transferApplyApi(id, params) {
  return request({
    url: `/fd_ask_transfer/${id}/apply`,
    method: 'put',
    data: params
  })
}
export function changeOnlineStatus(id, params) {
  return request({
    url: `/admin_user/change_online_status`,
    method: 'put',
    data: params
  })
}
// 获取转诊的人员列表
export function getChoice(sessionId) {
  return request({
    url: `/fd/health_ask/chat/${sessionId}/transfer_online_status`,
    method: 'get'
  })
}

// 转诊拒绝
export function rejectApi(id, params) {
  return request({
    url: `/fd_ask_transfer/${id}/reject`,
    method: 'put',
    data: params
  })
}
export function acceptApi(id, params) {
  return request({
    url: `/fd_ask_transfer/${id}/accept`,
    method: 'put',
    data: params
  })
}

// 发起转诊
export function startTransfer(id) {
  return request({
    url: `/fd_ask_transfer/${id}/start`,
    method: 'put'
  })
}
export function doctoreStartTransfer(id, data) {
  return request({
    url: `/fd_ask_transfer/${id}/start_doctor_voice`,
    method: 'put',
    data: data
  })
}
export function doctorCloseTransfer(id, data) {
  return request({
    url: `/fd_ask_transfer/${id}/end_doctor_voice`,
    method: 'put',
    data: data
  })
}
// 获取电话转诊的用户from 及 to 电话
export function getPhoneNumber(id) {
  return request({
    url: `/fd_ask_transfer/${id}/pls`,
    method: 'get'
  })
}

// 添加快捷回复
export function addFastReply(data) {
  return request({
    url: `/fast_reply`,
    method: 'post',
    data: data
  })
}
// 编辑
export function editFastReply(id, data) {
  return request({
    url: `/fast_reply/${id}`,
    method: 'put',
    data: data
  })
}
// 删除快捷回复
export function delFastReply(id) {
  return request({
    url: `/fast_reply/${id}`,
    method: 'delete'
  })
}
// 获取快捷回复列表
export function getFastReply() {
  return request({
    url: `/fast_reply/list`,
    method: 'get'
  })
}

// 获取代班列表
export function coverTableData(data) {
  return request({
    url: '/replace_work/list',
    method: 'get',
    params: data
  })
}
// 开始代班
export function startReplace(data) {
  return request({
    url: 'replace_work/start',
    method: 'post',
    data: data
  })
}
// 解除代班
export function closeReplace(data) {
  return request({
    url: '/replace_work/end',
    method: 'post',
    data: data
  })
}
