import request from '@/utils/request'
// 关闭食谱任务
export function closeTask(taskId, data) {
  return request({
    url: `/userRecipe/${taskId}/close`,
    method: 'put',
    data
  })
}
// 食谱留言回复
export function replayUser(taskId, data) {
  return request({
    url: `/userRecipe/replyUser/${taskId}`,
    method: 'post',
    data: data
    // mock: true
  })
}
// 查看详情
export function checkDesc(taskId, params = {}) {
  return request({
    url: `/userRecipe/checkDesc/${taskId}`,
    method: 'get',
    params: params
    // mock: true
  })
}
// 保存/发送食谱
export function upsetTask(id, type, data) {
  return request({
    url: `/userRecipe/upsetTask/${id}/type/${type}`,
    method: 'put',
    data
    // mock: true
  })
}
// 获取已保存的食谱任务详情
export function getTaskContent(taskId, params = {}) {
  return request({
    url: `/userRecipe/getSavedTask/${taskId}`,
    method: 'get',
    params: params
    // mock: true
  })
}

