import request from '@/utils/request'

export function getMembers(params) {
  return request({
    url: '/fd/members',
    method: 'get',
    params: params
  })
}
export function getSingleAskMembers(params) {
  return request({
    url: '/single_ask/members',
    method: 'get',
    params: params
  })
}
export function getFreedomMembers(params) {
  return request({
    url: '/fd/members/freedom',
    method: 'get',
    params: params
  })
}
export function getSingleAskFreedomMembers(params) {
  return request({
    url: '/single_ask/members/freedom',
    method: 'get',
    params: params
  })
}
export function getMemberDetail(id) {
  return request({
    url: `/fd/members/${id}`,
    method: 'get'
  })
}
export function getSingleAskMemberDetail(id) {
  return request({
    url: `/single_ask/members/${id}`,
    method: 'get'
  })
}
export function deleteMember(id) {
  return request({
    url: `/fd/members/${id}`,
    method: 'delete'
  })
}
export function deleteSingleAskMember(id) {
  return request({
    url: `/single_ask/members/${id}`,
    method: 'delete'
  })
}
export function createMember(params) {
  return request({
    url: '/fd/members',
    method: 'post',
    data: params
  })
}
export function createSingleAskMember(params) {
  return request({
    url: '/single_ask/members',
    method: 'post',
    data: params
  })
}
export function updateMember(id, params) {
  return request({
    url: `/fd/members/${id}`,
    method: 'put',
    data: params
  })
}
export function updateSingleAskMember(id, params) {
  return request({
    url: `/single_ask/members/${id}`,
    method: 'put',
    data: params
  })
}
export function updateMemberStatus(id, params) {
  return request({
    url: `/fd/members/${id}/status`,
    method: 'put',
    data: params
  })
}
export function SingleAskMemberStatus(id, params) {
  return request({
    url: `/single_ask/members/${id}/status`,
    method: 'put',
    data: params
  })
}
