/* eslint-disable prettier/prettier */
'use strict'

import store from '@/store'
import webIm from './webim'

class WebImHandler {
  constructor(opts) {
    this.accountMode = opts.accountMode || 0
    this.sdkAppID = opts.sdkAppID || ''
    this.avChatRoomId = opts.avChatRoomId || ''
    this.loginInfo = {}
  }

  // 监听大群新消息（普通，点赞，提示，红包）
  onBigGroupMsgNotify(msgList) {
    for (let i = msgList.length - 1; i >= 0; i--) {
      // 遍历消息，按照时间从后往前
      var msg = msgList[i]
      console.log('receive a new avchatroom group msg: ' + msg)
    }
  }

  onMsgNotify(newMsgList) {
    let content
    for (const j in newMsgList) {
      // 遍历新消息
      const newMsg = newMsgList[j]
      const message = newMsg.elems[0].getContent()
      content = {
        event: message.desc,
        message: message.data
      }
      this.handlderReceiveMsg(newMsg)
    }
    if (content && newMsgList.length > 0) {
      if (content.event === 'new_message') {
        store.dispatch('im/onMessageNotify', content.message, { root: true })
      } else if (content.event === 'event_message') {
        store.dispatch('im/onEventNotify', content.message, { root: true })
      } else if (content.event === 'new_question_message') {
        store.dispatch('ask/changeMessage', true) // 有问诊的新消息
        store.dispatch(
          'ask/singleAskNotify',
          newMsgList[0].elems[0].getContent(),
          { root: true }
        )
      }
    }
  }

  onKickedEventCall() {
    // console.log('被踢出了')
    store.dispatch('im/loginAsync', '', { root: true })
  }

  // 处理收到消息消息（私聊(包括普通消息和全员推送消息)，普通群(非直播聊天室)消息）
  handlderReceiveMsg(msg) {
    let fromAccount, fromAccountNick

    fromAccount = msg.getFromAccount()
    if (!fromAccount) {
      fromAccount = ''
    }
    fromAccountNick = msg.getFromAccountNick()
    if (!fromAccountNick) {
      fromAccountNick = fromAccount
    }

    // 解析消息
    // 获取会话类型
    // webim.SESSION_TYPE.GROUP-群聊，
    // webim.SESSION_TYPE.C2C-私聊，
    const sessType = msg.getSession().type()
    // 获取消息子类型
    // 会话类型为群聊时，子类型为：webim.GROUP_MSG_SUB_TYPE
    // 会话类型为私聊时，子类型为：webim.C2C_MSG_SUB_TYPE
    const subType = msg.getSubType()

    switch (sessType) {
      case webIm.SESSION_TYPE.C2C: // 私聊消息
        switch (subType) {
          case webIm.C2C_MSG_SUB_TYPE.COMMON: // c2c普通消息
            // 业务可以根据发送者帐号fromAccount是否为app管理员帐号，来判断c2c消息是否为全员推送消息，还是普通好友消息
            // 或者业务在发送全员推送消息时，发送自定义类型(webim.MSG_ELEMENT_TYPE.CUSTOM,即TIMCustomElem)的消息，在里面增加一个字段来标识消息是否为推送消息
            // contentHtml = convertMsgtoHtml(msg);
            // webIm.Log.warn('receive a new c2c msg: fromAccountNick=' + fromAccountNick + ", content=" + contentHtml);
            // c2c消息一定要调用已读上报接口
            var opts = {
              To_Account: fromAccount, // 好友帐号
              LastedMsgTime: msg.getTime() // 消息时间戳
            }
            webIm.c2CMsgReaded(opts)
            // console.error('收到一条c2c消息(好友消息或者全员推送消息): 发送人=' + fromAccountNick + ", 内容=" + contentHtml);
            break
        }
        break
      case webIm.SESSION_TYPE.GROUP: // 普通群消息，对于直播聊天室场景，不需要作处理
        break
    }
  }

  async login(userInfo, options) {
    return new Promise((resolve, reject) => {
      const listeners = {
        onConnNotify: '', // 选填
        onBigGroupMsgNotify: this.onBigGroupMsgNotify, // 监听新消息(大群)事件，必填
        onMsgNotify: this.onMsgNotify.bind(this), // 监听新消息(私聊(包括普通消息和全员推送消息)，普通群(非直播聊天室)消息)事件，必填
        onGroupSystemNotifys: '', // 监听（多终端同步）群系统消息事件，必填
        onGroupInfoChangeNotify: '', // 监听群资料变化事件，选填
        onKickedEventCall: this.onKickedEventCall.bind(this)
      }
      // web sdk 登录
      webIm.login(
        userInfo,
        listeners,
        options,
        function(resp) {
          resolve(resp)
        },
        function(err) {
          reject(err)
        }
      ) //
    })
  }

  async loginOut() {
    return new Promise((resolve, reject) => {
      webIm.logout(
        res => {
          resolve(res)
        },
        err => {
          reject(err)
        }
      )
    })
  }
}

export default WebImHandler
