import WebImHandler from '@/utils/im/index'
import $store from '../../store'
import _ from 'lodash'
import { analysisNewMessage } from '@/utils/myMessage/message.js'
const WEB_IM = {
  sdkAppID: process.env.VUE_APP_IM_APPID,
  appIDAt3rd: process.env.VUE_APP_IM_APPID,
  accountType: '',
  avChatRoomId: ''
}
const webImHandler = new WebImHandler(WEB_IM)

const state = {
  loginInfo: {},
  login_option: {
    isAccessFormalEnv: true, // 正式环境
    isLogOn: false // 日志输出
  }
}

const mutations = {
  updateLoginInfo(state, payload) {
    state.loginInfo.appIDAt3rd = WEB_IM.appIDAt3rd
    state.loginInfo.sdkAppID = WEB_IM.sdkAppID
    state.loginInfo.avChatRoomId = WEB_IM.avChatRoomId
    state.loginInfo.userSig = payload.user_sign
    state.loginInfo.identifier = payload.account
  }
}

const actions = {
  async loginAsync({ rootState }) {
    const { im } = rootState
    if (im.loginInfo.identifier) {
      await webImHandler.login(im.loginInfo, im.login_option)
    }
  },

  async loginOut() {
    await webImHandler.loginOut()
  },
  async onMessageNotify({ state }, payload) {
    $store.dispatch('chat/getCustomerTree', '', {
      root: true
    })
    const newMsg = JSON.parse(payload)
    const newMessage = analysisNewMessage(newMsg)
    const currentIMInfoMessages = $store.state.myIM.currentIMInfoMessages
    let existMesssage = {}
    let existMesssageIndex
    if (currentIMInfoMessages) {
      existMesssage = _.find(currentIMInfoMessages, function(o) {
        return o._id === newMessage._id
      })
      existMesssageIndex = _.findIndex(currentIMInfoMessages, function(o) {
        return o._id === newMessage._id
      })
    }
    if (existMesssage) {
      // 新消息存在，则替换
      const params = {
        oldMessage: existMesssage, // 作为保留字段，暂未使用
        oldMessageIndex: existMesssageIndex,
        newMessage: newMessage
      }
      $store.dispatch('replaceMessage', params)
    } else {
      // 新消息不存在，则push
      newMessage.duration = 5000
      // 有新消息来时，刷新会话列表，且全局通知
      $store.dispatch('notify', newMessage, {
        root: true
      })
      if (
        newMsg.fd_session === $store.state.msgQueue.sessionId &&
        $store.state.chat.activedBabyId === newMsg.family_member
      ) {
        $store.dispatch('pushCurrentIMInfoMessages', newMsg)
      } else {
        console.log(
          'babyId不一致，不需要加载消息',
          newMsg.family_member,
          $store.state.chat.activedBabyId
        )
      }
    }
    if (
      newMsg.fd_session === $store.state.msgQueue.sessionId &&
      $store.state.chat.activedBabyId === newMsg.family_member
    ) {
      await $store.dispatch('chat/getLastAsk', $store.state.msgQueue.sessionId)
    }
  },
  async onEventNotify({ state }, payload) {
    const message = JSON.parse(payload)
    switch (message.message_body.event) {
      case 'msg_read':
        if (
          message.message_body.message_id &&
          message.message_body.message_id.length > 0
        ) {
          for (let i = 0; i < message.message_body.message_id.length; i++) {
            $store.dispatch(
              'changeMessageReadSatus',
              message.message_body.message_id[i],
              {
                root: true
              }
            )
          }
        }
        break
      case 'path_add':
        // 事件通知刷新咨询轨迹
        if (
          $store.state.chat.lastAsk &&
          $store.state.chat.lastAsk._id === message.message_body.fd_ask
        ) {
          $store.dispatch('chat/getLastAsk')
          $store.dispatch('chat/getActivedUserInfo', {
            id: $store.state.chat.activedUserId
          })
          $store.dispatch(
            'chat/refreshTimeLineInfo',
            message.message_body.fd_ask
          )
        }
        break
      case 'session_refresh':
        // 用户或者服务端结束会话
        if (
          message.message_body.fd_session === $store.state.msgQueue.sessionId
        ) {
          $store.dispatch('chat/getCustomerTree', '', {
            root: true
          }) // 刷线当前列表的用户状态
        }
        break
      case 'msg_card':
        // console.log('有卡片')
        break
      case 'system_notice':
        message.duration = 0
        $store.dispatch('notify', message, {
          root: true
        })
        break
      default:
        break
    }
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
