import { getBabyList, getServiceDic } from '@/api/healthManagement'

const state = {
  babyList: [],
  sendStatus: 'select',
  taskCount: {},
  serviceDic: [],
  selectBaby: null,
  babyCount: null,
  currentPage: 1,
  pageSize: 10,
  paramsId: ''
}
const mutations = {
  SET_SELECTBABY: (state, baby) => {
    state.selectBaby = baby
  },
  SET_BABYLIST: (state, babyList) => {
    state.babyList = babyList
  },
  STE_SENDSTATUS: (state, status) => {
    state.sendStatus = status
  },
  SET_TASKCOUNT: (state, countObj) => {
    state.taskCount = countObj
  },
  SET_SERVICEDIC: (state, dic) => {
    state.serviceDic = dic
  },
  SET_BABYCOUNT: (state, count) => {
    state.babyCount = count
  },
  SET_CURRENTPAGE: (state, current) => {
    state.currentPage = current
  },
  SET_PAGESIZE: (state, size) => {
    state.pageSize = size
  }
}
const actions = {
  getBabyList: ({ commit, state }, params) => {
    const bayListParams = {
      ...params,
      page_size: state.pageSize,
      current_page: state.currentPage
    }
    getBabyList(bayListParams).then(res => {
      // 设置宝宝分页组件页码总数
      commit('SET_BABYCOUNT', res.data.count)
      // 设置宝宝列表数据
      commit('SET_BABYLIST', res.data.list)
    })
  },
  getServiceDic: ({ commit }) => {
    getServiceDic().then(res => {
      commit('SET_SERVICEDIC', res.data.services)
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
