import { getMsgQueueApi } from '@/api/bout.js'
import { Message } from 'element-ui'
import $store from '../../store'
import _ from 'lodash'
const msgQueue = {
  state: {
    // 当前聊天人的历史聊天记录
    msgQueue: [],
    sessionId: '',
    askingCustomerList: [], //咨询中的用户
    transferCustomerList: [], // 转诊中的用户
    waitingCustomerList: [] //咨询待接入、转诊待接入
  },
  mutations: {
    // 设置当前聊天对象的 历史聊天记录
    SET_MESSAGE_QUEUE: (state, messageQueue) => {
      state.msgQueue = messageQueue
    },
    SET_ASKING_LIST: (state, askingCustomerList) => {
      state.askingCustomerList = askingCustomerList
    },
    SET_TRANSFER_CUSTOMER: (state, data) => {
      state.transferCustomerList = data
    },
    SET_WAITING_CUSTOMER: (state, data) => {
      state.waitingCustomerList = data
    },
    SET_SESSION_ID: (state, sessionId) => {
      state.sessionId = sessionId
    }
  },
  actions: {
    getSessionObjById({ state }, sessionId) {
      let currentSessionObj = null
      if (sessionId) {
        currentSessionObj = _.find(state.msgQueue, function(o) {
          return o.session_id === sessionId
        })
      }
      return currentSessionObj
    },
    setCustomerInfo({ commit, dispatch }, customer) {
      dispatch('setSessionId', customer.session_id)
      commit('chat/SET_UNREAD_COUNT', 0)
      if (customer.clickTag === 'baby') {
        dispatch('chat/setActivedBabyById', {
          babyId: customer.babyId
        })
        return
      }
      dispatch(
        'chat/getActivedUserInfo',
        {
          id: customer.userId,
          babyId: customer.babyId
        },
        {
          root: true
        }
      )
    },
    async getMsgQueue({ state, commit, dispatch }, params) {
      const sId =
        params && params.sessionId ? params.sessionId : state.sessionId
      await getMsgQueueApi({
        session_id: sId
      }).then(res => {
        const list = res.data && res.data.list ? res.data.list : []
        if (list && list.length) {
          commit('SET_MESSAGE_QUEUE', list)
          dispatch('chatStatusFilter', list)
          const sessionId = sId || res.data.session_id
          dispatch('setSessionId', sessionId)
          if (params && params.type === 'init') {
            dispatch('chat/getActivedUserInfo', {
              babyId: params.babyId || list[0].family_member._id || '',
              id: params.userId || list[0].user._id || ''
            })
            if (params.messageId) {
              dispatch('goMessageDetail', params.messageId)
            }
          }
        } else {
          Message.info('消息队列数据为空')
        }
      })
    },
    async setSessionId({ commit, dispatch }, sessionId) {
      await commit('SET_SESSION_ID', sessionId)
      await dispatch('setSessionById', sessionId)
    },
    async setSessionById({ dispatch, commit }, sessionId) {
      await dispatch('setTrigger', 'changebaby', {
        root: true
      })
      const currentObj = await dispatch('getSessionObjById', sessionId)
      if (currentObj) {
        dispatch('chat/getLastAsk', sessionId)
        commit('chat/SET_CURRENT_SESSION', currentObj)
        commit('chat/IS_MY_USER', true)
      } else {
        dispatch('chat/getChat', sessionId, {
          root: true
        })
        // Message.info('没有获取到改sessionId对应的对象')
      }
    },
    chatStatusFilter: ({ commit, dispatch }, msgQueue) => {
      let hmWaitingCustomerList = []
      let transferWaitingCustomerList = []
      let askingCustomerList = []
      let transferCustomerList = []
      _(msgQueue).forEach(function(item) {
        if (item.chat_status === 'pending') {
          hmWaitingCustomerList.push(item)
        }
        if (item.chat_status === 'transfer_pending') {
          transferWaitingCustomerList.push(item)
        }
        if (item.chat_status === 'asking') {
          askingCustomerList.push(item)
        }
        if (item.chat_status === 'transfer_asking') {
          transferCustomerList.push(item)
        }
      })
      commit('SET_ASKING_LIST', askingCustomerList)
      commit('SET_TRANSFER_CUSTOMER', transferCustomerList)
      const waitingList =
        $store.state.user.transferRole === 'hm'
          ? hmWaitingCustomerList
          : transferWaitingCustomerList
      commit('SET_WAITING_CUSTOMER', waitingList)
    }
  }
}
export default msgQueue
