import Layout from '@/layout'

const perfix_rid = 'assistant'

export default {
  r_id: perfix_rid + 10001,
  path: '/official-account',
  component: Layout,
  hidden: false,
  meta: {
    title: '公众号管理',
    icon: 'gongzhonghao',
    activeMenu: '/assistant'
  },
  children: [
    {
      r_id: perfix_rid + 10014.1,
      path: '',
      name: 'officialAccount',
      hidden: true,
      component: () => import('@/views/system-settings/official-account/list'),
      meta: {
        title: '公众号管理',
        breadcrumb: false
      }
    },
    {
      r_id: perfix_rid + 10014.2,
      path: 'add-menu',
      name: 'addMenu',
      hidden: true,
      component: () =>
        import('@/views/system-settings/official-account/addMenu'),
      meta: {
        title: '新增菜单',
        breadcrumb: true,
        activeMenu: '/official-account'
      }
    },
    {
      r_id: perfix_rid + 10014.3,
      path: 'edit-menu/:id',
      name: 'editMenu',
      hidden: true,
      component: () =>
        import('@/views/system-settings/official-account/addMenu'),
      meta: {
        title: '编辑菜单',
        breadcrumb: true,
        activeMenu: '/official-account'
      }
    },
    {
      r_id: perfix_rid + 10014.4,
      path: 'add-keyword',
      name: 'addKeyword',
      hidden: true,
      component: () =>
        import('@/views/system-settings/official-account/addMenu'),
      meta: {
        title: '新增关键字',
        breadcrumb: true,
        activeMenu: '/official-account'
      }
    },
    {
      r_id: perfix_rid + 10014.5,
      path: 'edit-keyword/:id',
      name: 'editKeyword',
      hidden: true,
      component: () =>
        import('@/views/system-settings/official-account/addMenu'),
      meta: {
        title: '编辑关键字',
        breadcrumb: true,
        activeMenu: '/official-account'
      }
    }
  ]
}
