import Vue from 'vue'

import 'normalize.css' // A modern alternative to CSS resets

// 引入样式
import 'vue-easytable/libs/themes-base/index.css'
import 'viewerjs/dist/viewer.css'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import App from './App'
import store from './store'
import router from './router'

import * as filters from './filters' // global filters

import '@/assets/icons/iconfont.css'
import '@/icons' // icon
import '@/permission'
import '@/styles/index.scss' // global css
import '@/styles/bout.scss' // bout css

import myAudio from './components/audio'
import MyCopy from '@/components/MyCopy'
// 复制粘贴功能
import VueClipboard from 'vue-clipboard2' // permission control
import { formatTime, timeByIsBefore } from './utils/moment'
// 自定义clickoutside指令，判断鼠标是否点击了指令所绑定元素的外部
import clickoutside from '@/utils/clickoutside'
Vue.prototype.formatTime = formatTime
Vue.prototype.timeByIsBefore = timeByIsBefore

Vue.use(ElementUI)
Vue.use(myAudio)
Vue.component('my-copy', MyCopy)

Vue.directive('clickoutside', clickoutside)
// 自定义指令自动获取焦点
Vue.directive('myfocus', {
  inserted: function(el, binding) {
    el.focus()
  }
})
// register global utility filters
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})
Vue.use(VueClipboard)

Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})
