<template>
  <div class="app-header">
    <img class="app-header__logo" src="@/assets/logo.png" alt="logo" />
    <div
      v-clickoutside="clickoutside"
      class="app-header__quick-menu"
      style="position:relative"
    >
      <div v-show="visible" class="user_edit_popover">
        <p class="clearfix">
          <span class="popover_title">在线信息状态设置</span
          ><span class="popver_tip">仅公司内部同事可见</span>
        </p>
        <el-form
          ref="changeStatusForm"
          :model="userStatusForm"
          label-position="left"
          :rules="rules"
          label-width="110px"
          size="mini"
          @focus="visibleTrue"
        >
          <el-form-item label="选择状态类别" prop="online_status">
            <el-select
              v-model="userStatusForm.online_status"
              placeholder="选择状态类别"
              @focus="visibleTrue"
            >
              <el-option label="在线" value="online" />
              <el-option label="挂起" value="hang_up" />
            </el-select>
          </el-form-item>
          <el-form-item
            v-if="userStatusForm.online_status !== 'online'"
            label="状态说明"
            prop="hang_up_reason"
          >
            <el-input
              v-model="userStatusForm.hang_up_reason"
              maxlength="10"
              aria-placeholder="请输入挂起原因"
              type="text"
              show-word-limit
              @focus="visibleTrue"
            />
          </el-form-item>
          <el-form-item
            v-if="userStatusForm.online_status !== 'online'"
            label="上线时间"
            prop="predict_online_at"
          >
            <el-time-picker
              v-model="userStatusForm.predict_online_at"
              placeholder="请选择时间"
              style="width: 100%;"
              @focus="visibleTrue"
            />
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="confirmStatus">确 定</el-button>
            <el-button @click="visibleFalse">取 消</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="app-header__avatar-wrapper" @click="showMenuVisibleFun">
        <div
          class="app-header__user-avatar"
          :style="{ backgroundImage: `url(${avatar})` }"
        >
          <img
            class="header_status_icon"
            :src="getStatusImg(userInfo.online_status)"
          />
        </div>
        <span v-show="name" class="app-header__user-name">
          <span class="app-header_user_name">{{ name }}</span>
          <span
            v-if="myReplaceStatus.replace.status"
            class="replace_status"
            :class="myReplaceStatus.replace.status"
          >
            {{
              CoverStatus(
                myReplaceStatus.replace.status,
                myReplaceStatus.replace
              )
            }}
          </span>
          <br />
          <span class="user_role">{{ systemRole(bus_role, '') }}</span></span
        >
        <div v-show="showMenuVisible" class="show_menu_box">
          <div class="user_status">
            <p class="user_icon_name_status">
              <span class="user_name">{{ name }}</span>
              {{ systemRole(bus_role, '') }}
            </p>
            <p v-if="userInfo.online_status" class="user_edit">
              <img
                class="status_icon"
                :src="getStatusImg(userInfo.online_status)"
              />
              <span class="user_status"
                >&nbsp;&nbsp;{{
                  userInfo.online_status === 'online'
                    ? '我在线上'
                    : userStatus(userInfo.online_status)
                }}</span
              >
              &nbsp;&nbsp; &nbsp;&nbsp; &nbsp;&nbsp;
              <el-tooltip
                class="item"
                effect="dark"
                content="修改在线状态信息"
                placement="top"
              >
                <i
                  class="iconfont iconquanju_fankui"
                  style="cursor:pointer;"
                  @click="visibleTrue"
                />
              </el-tooltip>
            </p>
            <div
              v-if="
                userInfo.online_status != 'online' && userInfo.hang_up_reason
              "
              class="hang_up_reason_box"
            >
              <p class="hang_up_reason">{{ userInfo.hang_up_reason }}</p>
              <p v-if="userInfo.predict_online_at" class="hang_up_reason">
                预计上线时间：{{ seconds(userInfo.predict_online_at) }}
              </p>
            </div>
          </div>
          <div class="cover_set_btn" @click="showDialog">
            代班设置
          </div>
          <div class="border_bottom" />
          <div class="logout">
            <span style="display:block;" @click="logout">退出登录</span>
          </div>
        </div>
      </div>
    </div>
    <el-dialog
      title="代班设置"
      center
      :visible.sync="dialogVisible"
      :width="'540px'"
      class="cover_dialog"
    >
      <el-table :data="gridData" :header-row-class-name="'cover-table-th'">
        <el-table-column
          property="name"
          label="姓名"
          width="150"
          align="center"
        >
          <template slot-scope="scope">
            {{ scope.row.user.name }}
            <span v-if="scope.row.user.is_self">(本人)</span>
          </template>
        </el-table-column>
        <el-table-column
          property="date"
          label="状态"
          width="180"
          align="center"
        >
          <template slot-scope="scope">
            <span :class="'replace_' + scope.row.replace.status">
              {{
                scope.row.replace
                  ? CoverStatus(scope.row.replace.status, scope.row.replace)
                  : ''
              }}
            </span>
          </template>
        </el-table-column>
        <el-table-column label="代班或解除操作" width="160" align="center">
          <template slot-scope="scope">
            <el-button
              v-if="replaceOperation(scope.row).isClose"
              type="text"
              size="small"
              @click="closeReplace(scope.row)"
              >解除代班</el-button
            >
            <span v-else-if="replaceOperation(scope.row).isNone">无</span>
            <el-button
              v-else-if="
                myReplaceStatus.replace.status !== 'replace_by_other' &&
                  scope.row.user._id !== userInfo.id
              "
              type="text"
              size="small"
              @click="startReplace(scope.row)"
              >开始代班</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import { getStatusImg, userStatus, CoverStatus } from '@/filters'
import { seconds } from '@/utils/moment'
import { getUserHeart } from '@/api/user'
import { systemRole } from '@/utils/dictionary'

import {
  changeOnlineStatus,
  coverTableData,
  startReplace,
  closeReplace
} from '@/api/diagnose'
import _ from 'lodash'

export default {
  data() {
    return {
      dialogVisible: false,
      gridData: [],
      userStatusForm: {
        online_status: '',
        predict_online_at: '',
        hang_up_reason: ''
      },
      myReplaceStatus: {
        replace: {
          status: ''
        }
      },
      showMenuVisible: false,
      visible: false,
      rules: {
        online_status: [
          { required: true, message: '选择状态类别', trigger: 'change' }
        ],
        predict_online_at: [
          {
            type: 'date',
            required: true,
            message: '请选择上线日期',
            trigger: 'change'
          }
        ],
        hang_up_reason: [
          { required: true, message: '请输入挂起原因', trigger: 'change' }
        ]
      },
      watchUserStatusTimer: null
    }
  },
  computed: {
    ...mapGetters(['avatar', 'name', 'bus_role', 'activedBabyId', 'userInfo'])
  },
  watch: {
    visible() {
      if (this.visible === true) {
        this.showMenu()
        this.userStatusForm.online_status = '' // 接诊台 在线状态设置 ，每次进入编辑依然显示上一次 ，应重置（附截图）修改
        this.userStatusForm.hang_up_reason = ''
        this.userStatusForm.predict_online_at = ''
      } else if (this.visible === false) {
        this.handleClose()
      }
    },
    userInfo() {
      this.userStatusForm.online_status = this.userInfo.online_status
        ? this.userInfo.online_status
        : 'online'
    }
  },
  created() {
    this.getReplaceTable() // 设置当前用户的代班状态，存储到store中，
    this.userHeartBeat()
  },
  beforeDestroy() {
    if (this.watchUserStatusTimer) {
      clearInterval(this.watchUserStatusTimer)
    }
  },
  mounted() {
    const that = this
    const s = document.getElementsByClassName('user_edit_popover')[0]
    s.onmouseover = function() {
      if (that.visible) {
        that.showMenu()
      }
    }
  },
  methods: {
    systemRole,
    userStatus,
    getStatusImg,
    seconds,
    CoverStatus,
    clickoutside(e) {
      // console.log('点击元素外部区域，元素消失')
      this.visibleFalse()
    },
    userHeartBeat() {
      if (!this.watchUserStatusTimer) {
        this.watchUserStatusTimer = setInterval(() => {
          getUserHeart()
        }, 10000)
      }
    },
    replaceOperation(row) {
      const status = row.replace.status
      const be_replace = row.replace.be_replace
      let isClose = false
      let isNone = false
      switch (status) {
        case 'none':
          if (row.user._id === this.userInfo.id) {
            isNone = true
          }
          if (
            this.myReplaceStatus.replace.status === 'replace_by_other' &&
            status === 'none'
          ) {
            isNone = true
          }
          break
        case 'replace_other':
          if (
            be_replace &&
            be_replace.replace_user &&
            be_replace.replace_user._id === this.userInfo.id
          ) {
            isClose = true
          }
          isNone = true
          break
        case 'replace_by_other':
          if (
            be_replace &&
            be_replace.admin_user &&
            be_replace.admin_user._id === this.userInfo.id
          ) {
            isClose = true
          } else if (row.user._id === this.userInfo.id) {
            isClose = true
          }
          if (
            be_replace &&
            be_replace.replace_user &&
            be_replace.replace_user._id !== this.userInfo.id
          ) {
            isNone = true
          }
          break
      }
      return { isClose, isNone }
    },
    startReplace(row) {
      startReplace({ replace_user: row.user._id }).then(res => {
        this.updatedTableAndChatList(res)
      })
    },
    updatedTableAndChatList(res) {
      if (res) {
        this.getReplaceTable()
      }
      if (this.$route.path.indexOf('diagnose') !== -1) {
        this.$store.dispatch('chat/getCustomerTree', '', { root: true })
        this.$store.dispatch(
          'chat/getActivedUserInfo',
          { babyId: this.activedBabyId },
          { root: true }
        )
      }
    },
    closeReplace(row) {
      closeReplace({ replace_user: row.user._id }).then(res => {
        this.updatedTableAndChatList(res)
      })
    },
    showDialog() {
      this.getReplaceTable()
      this.dialogVisible = true
    },
    closeDialog() {
      this.dialogVisible = false
    },
    getReplaceTable() {
      coverTableData().then(res => {
        if (res && res.data.length > 0) {
          this.gridData = res.data
          for (let i = 0; i < res.data.length; i++) {
            if (res.data[i].user.is_self) {
              this.myReplaceStatus = res.data[i]
            }
          }
          this.$store.dispatch('user/setReplace', this.myReplaceStatus)
          this.gridData = _.orderBy(this.gridData, ['user.is_self'], ['desc']) // 自己排在最前面
        }
      })
    },
    showMenuVisibleFun() {
      this.showMenuVisible = true
    },
    visibleTrue() {
      this.visible = true
    },
    visibleFalse() {
      this.visible = false
      this.showMenuVisible = false
    },
    showMenu() {
      const dropmenu = document.getElementsByClassName('show_menu_box')[0]
      const user_edit_popover = document.getElementsByClassName(
        'user_edit_popover'
      )[0]
      setTimeout(() => {
        dropmenu.style.setProperty('display', 'block', 'important')
        if (
          this.userInfo.online_status === 'online' ||
          this.userInfo.online_status === 'offline'
        ) {
          user_edit_popover.style.setProperty('right', '183px', 'important')
        } else {
          user_edit_popover.style.setProperty('right', '256px', 'important')
        }
      }, 10)
    },
    handleClose() {
      const dropmenu = document.getElementsByClassName('show_menu_box')[0]
      dropmenu.style.setProperty('display', 'none', 'important')
    },
    confirmStatus() {
      this.$refs['changeStatusForm'].validate(valid => {
        if (valid) {
          changeOnlineStatus(this.userInfo.id, this.userStatusForm).then(
            res => {
              this.visibleFalse()
              if (res && res.data.result) {
                this.$store.dispatch('user/getInfo')
              }
            }
          )
        }
      })
    },
    async logout() {
      await this.$store.dispatch('user/logout')
      if (this.watchUserStatusTimer) {
        clearInterval(this.watchUserStatusTimer)
      }
      window.location.href =
        window.location.origin + '/login?redirect=dashboard'
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';
.user_edit_popover {
  position: absolute;
  top: 65px;
  right: 160px;
  padding: 20px 15px;
  width: 380px !important;
  background: #fff;
  border: 1px solid #ebeef5;
  border-radius: 4px;
  z-index: 200;
  -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
.app-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 60px;
  height: $headerHei;
  line-height: 1;
  box-shadow: 3px 0 19px 3px rgba(225, 225, 230, 0.5);
  &__logo {
    width: 140px;
    height: 40px;
  }
  &__quick-menu {
    padding-right: 10px;
    ul {
      @include clearfix;
      @include ff-medium;

      overflow: hidden;
      li {
        float: left;
        height: 50px;
      }
    }
  }
  &__news,
  &__avatar-wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
    .show_menu_box {
      position: absolute;
      top: 65px;
      right: 0;
      padding: 10px;
      min-width: 180px;
      background: #fff;
      border-radius: 4px;
      z-index: 200;
      box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
      cursor: auto;
      .user_status {
        position: relative;
        color: #17233d;
        .user_icon_name_status {
          line-height: 35px;
          font-size: 14px;
          font-family: PingFangSC-Regular;
          color: #17233d;
          letter-spacing: 0;
          margin-bottom: 5px;
          .user_name {
            line-height: 14px;
            font-size: 16px;
            font-family: PingFangSC-Medium;
            font-weight: bold;
            color: #17233d;
            letter-spacing: 0;
          }
        }
        .hang_up_reason_box {
          margin: 0 20px;
          padding: 10px 0;
          width: 195px;
          background: #f6f7fb;
          border-radius: 4px;
          .hang_up_reason,
          .predict_online_at {
            padding: 0 8px;
            line-height: 20px;
            font-size: 12px;
            font-family: PingFangSC-Regular;
            color: #999;
            letter-spacing: 0;
          }
        }
      }
      .user_edit {
        padding: 5px 0 3px;
        &:hover {
          cursor: pointer;
        }
      }
      .logout {
        line-height: 25px;
        cursor: pointer;
      }
    }
    .cover_set_btn {
      margin-top: 12px;
      margin-left: 22px;
      line-height: 14px;
      font-size: 14px;
      font-family: PingFangSC-Regular;
      color: #0085ff;
      letter-spacing: 0;
      cursor: pointer;
    }
    .border_bottom {
      margin: 10px 0;
      border-bottom: 1px solid #ebeef5;
    }
  }
  &__news {
    margin-right: 60px;
    font-size: 20px;
    &__icon {
      margin-right: 10px;
    }
  }
  &__user-avatar {
    position: relative;
    margin-right: 20px;
    width: 42px;
    height: 42px;
    background-position: center center;
    background-size: cover;
    border-radius: 50%;
    .header_status_icon {
      position: absolute;
      right: -2px;
      bottom: -2px;
      width: 14px;
      height: 14px;
    }
  }
  &__user-name {
    .app-header_user_name {
      position: relative;
      top: 2px;
      display: inline-block;
      margin-top: 5px;
      margin-right: 6px;
      font-size: 16px;
      font-weight: 600;
      color: #17233d;
      letter-spacing: 0;
      margin-bottom: 5px;
    }
    .user_role {
      line-height: 12px;
      font-size: 12px;
      font-family: PingFangSC-Regular;
      color: #17233d;
      letter-spacing: 0;
    }
    .replace_status {
      display: inline-block;
      padding: 1px 3px;
      line-height: 18px;
      font-size: 12px;
      font-family: PingFangSC-Regular;
      text-align: center;
      border-radius: 2px;
      letter-spacing: 0;
    }
    .replace_other {
      color: #f8747f;
      background: rgba(248, 116, 127, 0.06);
      border: 1px solid #f8747f;
    }
    .none {
      color: #0085ff;
      background: rgba(0, 133, 255, 0.06);
      border: 1px solid #0085ff;
    }
    .replace_by_other {
      color: #ffa21e;
      background: rgba(255, 162, 30, 0.06);
      border: 1px solid #ffa21e;
    }
  }
}
.el-dialog__wrapper .el-dialog__body {
  padding: 20px 25px 1px;
}
</style>
