<template>
  <div class="tip-list">
    <template v-for="item of notificationList">
      <message-tips
        v-if="
          item.type === '-1' &&
            item.status === 'normal' &&
            $store.state.user.mts_user.type === 'teacher'
        "
        :key="item._id"
        title="提示"
        footer-title="务必下班前完成"
        btn-text="立即前往"
        @btnClose="btnFollowUpClose($event, item)"
        @btnCommit="btnFollowUpCommit(item)"
      >
        <div slot="content">
          <p class="info">有人完成试岗阶段任务，需要进行试岗阶段情况跟进</p>
        </div>
      </message-tips>
      <message-tips
        v-if="
          item.type === '-2' &&
            item.status === 'normal' &&
            $store.state.user.mts_user.type === 'doctor'
        "
        :key="item._id"
        title="提示"
        footer-title="务必下班前完成"
        btn-text="立即前往"
        @btnClose="btnFollowUpClose($event, item)"
        @btnCommit="btnFollowUpCommit(item)"
      >
        <div slot="content">
          <p class="info">有人完成轮岗阶段任务，需要填写考核评价</p>
        </div>
      </message-tips>
      <message-tips
        v-if="
          item.type === '-1' &&
            item.status === 'normal' &&
            $store.state.user.mts_user.type === 'doctor'
        "
        :key="item._id"
        title="提示"
        footer-title="务必下班前完成"
        btn-text="立即前往"
        @btnClose="btnFollowUpClose($event, item)"
        @btnCommit="btnFollowUpCommit(item)"
      >
        <div slot="content">
          <p class="info">有人完成试岗阶段任务，需要填写考核评价</p>
        </div>
      </message-tips>
      <message-tips
        v-if="
          item.type === '-1' &&
            item.status === 'normal' &&
            $store.state.user.mts_user.type === 'human_resource'
        "
        :key="item._id"
        title="提示"
        footer-title="务必下班前完成"
        btn-text="立即前往"
        @btnClose="btnFollowUpClose($event, item)"
        @btnCommit="btnFollowUpCommit(item)"
      >
        <div slot="content">
          <p class="info">有人完成试岗阶段任务，需要填写考核评价</p>
        </div>
      </message-tips>
      <message-tips
        v-if="
          item.type !== '-1' &&
            item.type !== '-2' &&
            item.status === 'normal' &&
            $store.state.user.mts_user.type === 'teacher'
        "
        :key="item._id"
        title="提示"
        @btnClose="btnFollowUpClose($event, item)"
      >
        <div slot="content">
          <p class="info">
            有人已经完成第{{
              getStage(item.type)
            }}阶段任务，需要立即进行阶段考试
          </p>
          <p class="info">请尽快安排考试</p>
        </div>
      </message-tips>
    </template>
  </div>
</template>

<script>
import MessageTips from '@/components/MessageTips'
import { minimize } from '@/api/learningAdmin'
export default {
  components: {
    MessageTips
  },
  props: {
    notificationList: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  computed: {
    getStage() {
      return function(stage) {
        if (stage === '0') return '一'
        if (stage === '1') return '二'
        if (stage === '2') return '三'
        if (stage === '3') return '四'
        if (stage === '4') return '五'
        if (stage === '5') return '六'
      }
    }
  },
  methods: {
    async btnFollowUpClose(e, item) {
      e.target.parentElement.parentElement.style.right = '-400px'
      const { data, code } = await minimize({
        id: item._id
      })
      if (code === 20000 && data.result) {
        this.$emit('obtainNotification')
      }
    },
    btnFollowUpCommit(item) {
      if (this.$route.name === 'studentsList') {
        window.location.href = '/learning-admin/studentsList?type=' + item.type
      } else this.$router.push('/learning-admin/studentsList?type=' + item.type)
    }
  }
}
</script>
