<template>
  <div class="copy">
    <span
      v-clipboard:copy="copyKey"
      v-clipboard:success="onCopy"
      v-clipboard:error="onError"
    >
      复制</span
    >
  </div>
</template>
<script>
export default {
  data() {
    return {}
  },
  props: {
    copyKey: {
      type: String,
      default: ''
    }
  },
  methods: {
    // 复制成功
    onCopy(e) {
      this.$message.success('复制成功')
    },
    // 复制失败
    onError(e) {
      this.$message.success('复制失败')
    }
  }
}
</script>
<style lang="scss">
.copy {
  display: inline-block;
  margin-left: 4px;
  color: #409eff;
  cursor: pointer;
}
</style>
