import Layout from '@/layout'
// import Parent from '@/components/Parent/index.vue'
const perfix_rid = 'fd-setting'

export default {
  r_id: perfix_rid + 1001,
  path: '/fd-setting',
  component: Layout,
  redirect: '/fd-setting/FdServiceType',
  alwaysShow: true,
  name: 'fdSetting',
  meta: {
    title: '家庭医生配置',
    icon: 'plguanli',
    activeMenu: '/fd-setting/FdServiceType'
  },
  children: [
    {
      r_id: perfix_rid + 1007111,
      path: 'FdServiceType',
      component: () => import('@/views/system-settings/FdServiceType/index'), // Parent router-view
      meta: {
        title: '套餐管理',
        icon: 'coinpurse'
      },
      children: [
        {
          r_id: perfix_rid + 1008111,
          path: '',
          component: () => import('@/views/system-settings/FdServiceType/list'),
          name: 'list',
          hidden: true,
          meta: {
            activeMenu: '/fd-setting/FdServiceType'
          }
        },
        {
          r_id: perfix_rid + 1009111,
          path: 'add',
          component: () => import('@/views/system-settings/FdServiceType/add'),
          name: 'FdServiceAdd',
          hidden: true,
          meta: {
            title: '新增套餐',
            activeMenu: '/fd-setting/FdServiceType'
          }
        },
        {
          r_id: perfix_rid + 10010111,
          path: ':id/edit/:type',
          hidden: true,
          component: () => import('@/views/system-settings/FdServiceType/add'),
          name: 'FdServiceEdit',
          meta: {
            title: '套餐编辑',
            activeMenu: '/fd-setting/FdServiceType'
          }
        },
        {
          r_id: perfix_rid + 10010112,
          path: 'versionHistory',
          hidden: true,
          component: () =>
            import('@/views/system-settings/FdServiceType/history'),
          name: 'versionHistory',
          meta: {
            title: '套餐历史版本',
            activeMenu: '/fd-setting/FdServiceType'
          }
        }
      ]
    },
    {
      r_id: perfix_rid + 1001.3222,
      path: 'professional-role',
      component: () => import('@/views/system-settings/professional-role'),
      meta: {
        title: '业务角色管理',
        icon: 'namecard-fill'
      },
      children: [
        {
          r_id: perfix_rid + 1001.31222,
          path: '',
          hidden: true,
          name: 'professionalRoleList',
          component: () =>
            import('@/views/system-settings/professional-role/main'),
          meta: {
            title: '业务角色管理',
            breadcrumb: false,
            activeMenu: '/fd-setting/professional-role'
          }
        },
        {
          r_id: perfix_rid + 1001.33222,
          path: ':id/edit/:role',
          hidden: true,
          name: 'professionalRoleEdit',
          component: () =>
            import('@/views/system-settings/professional-role/edit'),
          meta: {
            title: '新增成员',
            activeMenu: '/fd-setting/professional-role'
          }
        }
      ]
    },
    {
      r_id: perfix_rid + 1001.4333,
      path: 'fd-team',
      component: () => import('@/views/system-settings/fd-team'),
      meta: {
        title: '家庭医生团队管理',
        icon: 'FDTeam'
      },
      children: [
        {
          r_id: perfix_rid + 1001.41333,
          path: '',
          hidden: true,
          name: 'FDTeamList',
          component: () => import('@/views/system-settings/fd-team/list'),
          meta: {
            title: '家庭医生团队管理',
            breadcrumb: false,
            activeMenu: '/fd-setting/fd-team'
          }
        },
        {
          r_id: perfix_rid + 1001.42333,
          path: 'add/:type',
          hidden: true,
          name: 'FDTeamAdd',
          component: () => import('@/views/system-settings/fd-team/add-team'),
          meta: {
            title: '新增家庭医生团队',
            activeMenu: '/fd-setting/fd-team'
          }
        },
        {
          r_id: perfix_rid + 1001.43333,
          path: ':id/edit/:type',
          hidden: true,
          name: 'FDTeamEdit',
          component: () => import('@/views/system-settings/fd-team/add-team'),
          meta: {
            title: '编辑家庭医生团队',
            activeMenu: '/fd-setting/fd-team'
          }
        }
      ]
    }
  ]
}
