import request from '@/utils/request'

// 客户管理设置相关api

// 客户类型
// 新建
export function addUserType(params) {
  return request({
    url: '/crm/basic/user_type',
    method: 'post',
    data: params
  })
}
// 编辑
export function putUserType(params, type) {
  const url =
    type === 'status'
      ? `/crm/basic/user_type/${params._id}/status`
      : `/crm/basic/user_type/${params._id}`
  return request({
    url: url,
    method: 'put',
    data: params
  })
}
// 获取选项
export function getUserTypeOptionApi(params) {
  return request({
    url: `/crm/basic/user_type/options`,
    method: 'get',
    params: params
  })
}
// 列表
export function getUserTypeList(params) {
  return request({
    url: '/crm/basic/user_type/list',
    method: 'get',
    params: params
  })
}

