import { getBabyDetailById } from '@/api/userInfo'

const state = {
  // 扩展区域当前激活的标签，默认是宝宝资料
  activeInfo: 'sharingNotes',
  // 调用扩展区的组件类型，默认是接诊台
  pageType: 'chat',
  babyDetailById: {}
}

const mutations = {
  SET_ACTIVEINFO: (state, activeTab) => {
    state.activeInfo = activeTab
  },
  SET_PAGETYPE: (state, pageType) => {
    state.pageType = pageType
  },
  SET_BABYDETAIL_BY_ID: (state, data) => {
    state.babyDetailById = data
  }
}
const actions = {
  getBabyDetailById({ commit }, id) {
    if (id) {
      getBabyDetailById(id).then(res => {
        commit('SET_BABYDETAIL_BY_ID', res.data[0])
      })
    }
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
