import Layout from '@/layout'

const perfix_rid = 'marketing'

export default {
  r_id: perfix_rid + 1001,
  path: '/marketing',
  component: Layout,
  redirect: '/marketing/coupon',
  alwaysShow: true,
  name: 'Marketing',
  meta: {
    title: '营销管理',
    icon: 'yxguanli'
  },
  children: [
    {
      r_id: perfix_rid + 1002,
      path: 'coupon',
      component: { render: h => h('router-view') },
      meta: {
        title: '优惠券管理',
        icon: 'yhqguanli',
        breadcrumb: true
      },
      children: [
        {
          r_id: perfix_rid + 100201,
          path: '',
          hidden: true,
          component: () => import('@/views/marketing/coupon'),
          meta: { title: '优惠券管理', breadcrumb: false }
        },
        {
          r_id: perfix_rid + 100202,
          path: 'add',
          name: 'CouponAdd',
          hidden: true,
          component: () => import('@/views/marketing/coupon/add/index'),
          meta: {
            title: '新建优惠券',
            breadcrumb: true,
            activeMenu: '/marketing/coupon'
          }
        }
      ]
    },
    {
      r_id: perfix_rid + 1003,
      path: 'rights',
      component: { render: h => h('router-view') },
      meta: {
        title: '权益管理',
        icon: 'qyguanlui'
      },
      children: [
        {
          r_id: perfix_rid + 100301,
          path: '',
          hidden: true,
          name: 'RightsList',
          component: () => import('@/views/marketing/rights'),
          meta: { title: '权益管理', breadcrumb: false }
        },
        {
          r_id: perfix_rid + 100302,
          path: 'detail/:type/:id',
          name: 'RightsAdd',
          hidden: true,
          component: () => import('@/views/marketing/rights/add/index'),
          meta: {
            title: '新增权益',
            breadcrumb: true,
            activeMenu: '/marketing/rights'
          }
        }
      ]
    }
  ]
}
