// boutCardData
export function boutCardData(cardContent, accountType, ind, reAsk) {
  const haveReAsk =
    reAsk && reAsk.content[0].ask_type === 're_ask' ? true : false
  let boutCardArr = []
  let showConformBtn
  if (haveReAsk && cardContent.ask_type === 're_ask') {
    showConformBtn = false
  } else if (cardContent.ask_type === 'new_ask') {
    showConformBtn = true
  } else {
    showConformBtn = false
  }
  boutCardArr.push({
    index: 1,
    type: 'confirm_text',
    show: false,
    content: '请确认本次咨询主要诉求'
  })
  boutCardArr.push({
    index: 12,
    type: 'btn',
    show: showConformBtn,
    content: '已确认'
  })
  _.forEach(cardContent, (value, key) => {
    if (key === 'ask_type') {
      // 是否显示首诊及复诊
      boutCardArr.push({
        index: 2,
        type: 'ask_type',
        show: cardContent.question_type === 'disease' ? true : false,
        content: askTypeFilter(value),
        showTime: true,
        time: formatTime(cardContent.created_at)
      })
    } else if (key === 'question_desc') {
      boutCardArr.push({
        index: 3,
        title: '问题描述',
        type: 'text',
        key: 'question_desc',
        dataArr: value.media, //问题描述相关图片或视频
        show: true,
        content: value.content ? value.content : value
      })
    } else if (key === 'ask_duration') {
      boutCardArr.push({
        index: 4,
        title: '症状持续时长',
        key: 'duration',
        type: 'text',
        show: value ? true : false,
        content: value ? value + '天' : '无'
      })
    } else if (key === 'has_intervene') {
      boutCardArr.push({
        index: 5,
        title: '干预史',
        key: 'intervene',
        content:
          value && cardContent.intervene_desc
            ? cardContent.intervene_desc.content
            : '无',
        type: 'text',
        show: true,
        dataArr:
          value && cardContent.intervene_desc
            ? cardContent.intervene_desc.media
            : [] //问题描述相关图片或视频
      })
    } else if (key === 'state_change') {
      boutCardArr.push({
        index: 6,
        title: '症状从出现到现在的变化',
        key: 'change',
        type: 'text',
        show: value ? true : false,
        content: stateChange(value)
      })
    } else if (key === 'question_type') {
      boutCardArr.push({
        index: 7,
        title: '问题类型',
        type: 'text',
        show: false,
        key: 'question_type',
        content: value
      })
    } else if (key === 'content') {
      // let section = ind + 1
      boutCardArr.push({
        index: 8,
        type: 'text',
        title: accountType === 'User' ? '问题描述' : '',
        // accountType === 'User' ? '问题描述' : '第' + section + '段回复',
        show: true,
        key: 'content',
        content: value
      })
    } else if (key === 'media') {
      boutCardArr.push({
        title: '图片及视频',
        show: true,
        key: 'media',
        index: 9,
        dataArr: value
      })
    } else if (key === 'voice') {
      if (value.url) {
        boutCardArr.push({
          index: 10,
          title: '',
          type: 'voice',
          show: true,
          key: 'voice',
          voice: value
        })
      }
    }
  })
  boutCardArr = _.orderBy(boutCardArr, ['index'], ['asc'])
  return boutCardArr
}
// 字符串的下划线格式转驼峰格式，eg：hello_world => helloWorld
export function underline2Hump(s) {
  return s.replace(/_(\w)/g, function(all, letter) {
    return letter.toUpperCase()
  })
}

// 字符串的驼峰格式转下划线格式，eg：helloWorld => hello_world
export function hump2Underline(s) {
  return s.replace(/([A-Z])/g, '_$1').toLowerCase()
}

// JSON对象的key值转换为驼峰式
export function jsonToHump(obj) {
  if (obj instanceof Array) {
    obj.forEach(function(v, i) {
      jsonToHump(v)
    })
  } else if (obj instanceof Object) {
    Object.keys(obj).forEach(function(key) {
      var newKey = underline2Hump(key)
      if (newKey !== key) {
        obj[newKey] = obj[key]
        delete obj[key]
      }
      jsonToHump(obj[newKey])
    })
  }
}

// JSON对象的key值转换为下划线格式
export function jsonToUnderline(obj) {
  if (obj instanceof Array) {
    obj.forEach(function(v, i) {
      jsonToUnderline(v)
    })
  } else if (obj instanceof Object) {
    Object.keys(obj).forEach(function(key) {
      var newKey = hump2Underline(key)
      if (newKey !== key) {
        obj[newKey] = obj[key]
        delete obj[key]
      }
      jsonToUnderline(obj[newKey])
    })
  }
}
