// 转诊状态文字显示判断
export function TransferStatusText(value, user, type, isAuto, role) {
  switch (value) {
    case 'apply':
      if (role === 'hm') {
        return '转诊申请待' + user.name + '确认'
      } else {
        return ''
      }
    case 'accept':
      if (role === 'hm') {
        if (isAuto) {
          return user.name + '转诊确认超时，自动同意可向用户发起转诊'
        }
        return user.name + '同意转诊，可向用户发起转诊'
      } else {
        return ''
      }
    case 'wait_user':
      return '等待用户选择转诊方式'
    case 'start':
      if (isAuto) {
        return (
          user.name + '转诊确认超时，自动' + TransferType(type) + '转诊成功'
        )
      }
      return user.name + TransferType(type) + '转诊成功'
    case 'failed':
      return '用户超时未选择转诊方式，转诊失败'
    case 'asking':
      return user.name + TransferType(type) + '转诊成功'
    case 'end':
    case 'reject':
      return ''
    default:
      return ''
  }
}
// 转诊状态时间显示判断
export function TransferStatusTime(value, lastAsk, isAuto, role) {
  let startTime = ''
  let endTime = ''
  switch (value) {
    case 'apply':
      if (role === 'hm') {
        startTime = lastAsk.transfer_apply_at
        endTime = timeAddMinute(startTime, 5)
      }
      break
    case 'wait_user':
      startTime = lastAsk.transfer_wait_user_at
      endTime = timeAddMinute(startTime, 5)
      break
    case 'start':
      // 转诊申请超时，自动同意后，语音/图文转诊提醒文案及计时 ,针对三个角色
      if (isAuto) {
        startTime = lastAsk.transfer_start_at
      }
      startTime = lastAsk.transfer_start_at
      break
    case 'accept':
    case 'reject':
    case 'fail':
    case 'end':
      break
    case 'asking':
      startTime = lastAsk.transfer_start_at
      break
    default:
      return ''
  }
  const time = {
    startTime: startTime,
    endTime: endTime
  }
  return time
}
// 用户列表，图文或电话转诊图标显示
export function UserListTransferStatusIcon(session) {
  let dotColor = ''
  let iconType = ''
  let borderColor = ''
  let content = ''
  const value = session.last_ask.transfer_status
  const type = session.last_ask.transfer_type
  switch (value) {
    case 'start':
      if (session.status === 'asking' && session.last_ask.has_transfer) {
        if (type === 'doctor_voice') {
          // 电话转诊成功
          dotColor = '#FFFFFF'
          iconType = 'icon-dhzhuanzhen'
          borderColor = '#ED3645'
          content = '电话转诊未开始'
        } else {
          // 图文转诊成功
          dotColor = '#FFFFFF'
          iconType = 'icon-twzhuanzhen'
          borderColor = '#ED3645'
          content = '图文转诊待回复'
        }
      }
      break
    case 'asking':
      if (session.status === 'asking' && session.last_ask.has_transfer) {
        if (type === 'doctor_voice') {
          // 电话转诊开始
          dotColor = '#FFFFFF'
          iconType = 'icon-dhzhuanzhen'
          borderColor = '#0085FF'
          content = '电话转诊已开始'
        } else {
          // 图文转诊开始
          dotColor = '#FFFFFF'
          iconType = 'icon-twzhuanzhen'
          borderColor = '#0085FF'
          content = '图文转诊已回复'
        }
      }
      break
  }
  return {
    dotColor,
    iconType,
    borderColor,
    content
  }
}
// val:指定日期, minuteVal：要增加的minute
export function timeAddMinute(val, minuteVal) {
  // 2. 获取指定时间
  var date = new Date(val)
  var min = date.getMinutes()
  // 3. 设置当前时间+5分钟：把当前分钟数+5后的值重新设置为date对象的分钟数
  date.setMinutes(min + minuteVal)
  // 4. 测试
  return date
}
