<template>
  <div class="message-tips">
    <div class="group center">
      <h2 class="message-title">{{ title }}</h2>
      <div class="content">
        <p class="info">
          <slot name="content" />
          <el-button
            v-if="btnText"
            type="primary"
            @click="$emit('btnCommit')"
            >{{ btnText }}</el-button
          >
        </p>
        <p v-if="footerTitle" class="footer-title">{{ footerTitle }}</p>
      </div>
      <span class="icon" :class="iconName" @click="$emit('btnClose', $event)" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    footerTitle: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    iconName: {
      type: String,
      default: 'el-icon-minus'
    },
    btnText: {
      type: String,
      default: ''
    }
  }
}
</script>

<style scoped lang="scss">
.message-tips {
  position: fixed;
  right: -400px;
  display: flex;
  overflow: hidden;
  padding: 14px 0;
  width: 400px;
  background-color: #fff;
  border: 1px solid #ebeef5;
  border-radius: 8px;
  z-index: 999;
  -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  transition: opacity 0.3s, left 0.3s, right 0.3s, top 1s, bottom 0.3s,
    -webkit-transform 0.3s;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  .group {
    margin-right: 8px;
    margin-left: 13px;
    width: 100%;
    &.center {
      text-align: center;
    }
    .message-title {
      margin: 0;
      padding: 0 0 10px;
      font-size: 16px;
      font-weight: 700;
      color: #303133;
      border-bottom: 1px solid #ebeef5;
    }
    .content {
      margin: 6px 0 0;
      line-height: 21px;
      font-size: 14px;
      text-align: center;
      color: #606266;
      .info {
        margin: 15px 0 10px;
        .el-button {
          padding: 6px 20px;
          height: 32px;
        }
      }
    }
    .icon {
      position: absolute;
      top: 15px;
      right: 15px;
      font-size: 16px;
      color: #909399;
      cursor: pointer;
    }
    .footer-title {
      margin-top: 10px;
      font-size: 12px;
      color: #999;
      &::before {
        margin-right: 4px;
        color: #f56c6c;
        content: '*';
      }
    }
  }
}
</style>
