import Layout from '@/layout'
import Parent from '@/components/Parent/index.vue'
const perfix_rid = 'evaluate'

export default {
  r_id: perfix_rid + 1001,
  path: '/evaluate',
  component: Layout,
  redirect: '/evaluate/MedicalService',
  alwaysShow: true,
  name: 'evaluate',
  meta: {
    title: '评价管理',
    icon: 'plguanli',
    activeMenu: '/evaluate/MedicalService'
  },
  children: [
    {
      r_id: perfix_rid + 1002,
      path: 'MedicalService',
      component: Parent,
      meta: {
        title: '家庭医生用户评价',
        icon: 'jtyspinglun',
        name: 'MedicalService',
        activeMenu: '/evaluate/MedicalService',
        breadcrumb: true
      },
      children: [
        {
          r_id: perfix_rid + 100201,
          path: '',
          hidden: true,
          component: () => import('@/views/evaluate/MedicalService/list.vue'),
          meta: {
            title: '评价列表',
            breadcrumb: false,
            name: 'MedicalService',
            activeMenu: '/evaluate/MedicalService',
            keepAlive: true
          }
        },
        {
          r_id: perfix_rid + 100202,
          path: 'detail/:id',
          hidden: true,
          component: () => import('@/views/evaluate/MedicalService/detail.vue'),
          meta: {
            title: '评价详情',
            breadcrumb: true,
            name: 'MedicalService',
            activeMenu: '/evaluate/MedicalService'
          }
        }
      ]
    },
    {
      r_id: perfix_rid + 1003,
      path: 'diagnose',
      component: Parent,
      meta: {
        title: '问诊用户评价',
        name: 'diagnose',
        icon: 'wzpinglun'
      },
      children: [
        {
          r_id: perfix_rid + 100301,
          path: '',
          hidden: true,
          component: () => import('@/views/evaluate/diagnose/list.vue'),
          meta: {
            title: '评价列表',
            name: 'diagnose',
            breadcrumb: true,
            activeMenu: '/evaluate/diagnose',
            keepAlive: true
          }
        },
        {
          r_id: perfix_rid + 100302,
          path: 'detail/:id',
          hidden: true,
          component: () => import('@/views/evaluate/diagnose/detail.vue'),
          meta: {
            title: '评价详情',
            name: 'diagnose',
            breadcrumb: true,
            activeMenu: '/evaluate/diagnose'
          }
        }
      ]
    }
  ]
}
