import Layout from '@/layout'

const perfix_rid = 'systemSettings'

export default {
  r_id: perfix_rid + 1001,
  path: '/system-settings',
  component: Layout,
  redirect: '/system-settings/user',
  alwaysShow: true,
  name: 'SystemSettings',
  meta: {
    title: '系统设置',
    icon: 'setting-fill'
  },
  children: [
    {
      r_id: perfix_rid + 1002,
      path: 'user',
      component: () => import('@/views/system-settings/user/index'),
      meta: {
        title: '成员管理',
        icon: 'meeting-fill'
      },
      children: [
        {
          r_id: perfix_rid + 1003,
          path: '',
          hidden: true,
          name: 'UserList',
          component: () => import('@/views/system-settings/user/list'),
          meta: {
            title: '成员管理',
            breadcrumb: false,
            icon: 'tree'
          }
        },
        {
          r_id: perfix_rid + 1004,
          path: ':id/conlog',
          hidden: true,
          name: 'UserConlog',
          component: () => import('@/views/system-settings/user/conlog'),
          meta: {
            title: '登录日志',
            icon: 'tree',
            activeMenu: '/system-settings/user'
          }
        },
        {
          r_id: perfix_rid + 1005,
          path: ':id/edit',
          hidden: true,
          name: 'UserEdit',
          component: () => import('@/views/system-settings/user/edit'),
          meta: {
            title: '编辑成员信息',
            icon: 'tree',
            activeMenu: '/system-settings/user'
          }
        }
      ]
    },
    {
      r_id: perfix_rid + 1006,
      path: 'role',
      component: () => import('@/views/system-settings/role/index'),
      name: 'RoleManage',
      meta: {
        title: '权限组管理',
        icon: 'icon_quanxian'
      }
    },
    {
      r_id: perfix_rid + 1001.5,
      path: 'service',
      component: () => import('@/views/system-settings/service'),
      meta: {
        title: '服务管理',
        icon: 'shakehands_fill'
      },
      children: [
        {
          r_id: perfix_rid + 1001.51,
          path: '',
          hidden: true,
          name: 'ServiceList',
          component: () => import('@/views/system-settings/service/list'),
          meta: {
            title: '服务管理',
            breadcrumb: false,
            activeMenu: '/system-settings/service'
          }
        },
        {
          r_id: perfix_rid + 1001.52,
          path: ':id/handle-conlog',
          hidden: true,
          name: 'ServiceHandleConlog',
          component: () =>
            import('@/views/system-settings/service/handle-conlog'),
          meta: {
            title: '操作日志',
            activeMenu: '/system-settings/service'
          }
        }
      ]
    },
    {
      r_id: perfix_rid + 10012,
      path: 'plan',
      component: () => import('@/views/system-settings/plan/index'),
      meta: {
        title: '计划任务管理',
        icon: 'jiankangguanli'
      }
    },
    {
      r_id: perfix_rid + 100101,
      path: 'grow-template',
      meta: {
        title: '发育量表模板管理',
        breadcrumb: true,
        icon: 'tree'
      },
      component: () => import('@/views/system-settings/grow-template/index'),
      children: [
        {
          r_id: perfix_rid + 100101.1,
          path: '',
          hidden: true,
          component: () => import('@/views/system-settings/grow-template/list'),
          meta: {
            title: '',
            activeMenu: '/system-settings/grow-template'
          }
        },
        {
          r_id: perfix_rid + 100101.2,
          path: 'add',
          hidden: true,
          name: 'addTemplate',
          component: () =>
            import('@/views/system-settings/grow-template/addTemplate'),
          meta: {
            title: '新增模版',
            activeMenu: '/system-settings/grow-template'
          }
        },
        {
          r_id: perfix_rid + 100102.2,
          path: 'edit/:id',
          hidden: true,
          name: 'editTemplate',
          component: () =>
            import('@/views/system-settings/grow-template/addTemplate'),
          meta: {
            title: '编辑模版',
            activeMenu: '/system-settings/grow-template'
          }
        },
        {
          r_id: perfix_rid + 100103.2,
          path: 'look/:id',
          hidden: true,
          component: () =>
            import('@/views/system-settings/grow-template/lookTemplate'),
          meta: {
            title: '查看模版',
            activeMenu: '/system-settings/grow-template'
          }
        }
        // /lookTemplate
      ]
    },
    {
      r_id: perfix_rid + 10003,
      path: 'recipes',
      component: {
        render: h => h('router-view')
      },
      meta: {
        title: '食谱模板管理',
        breadcrumb: false,
        icon: 'spmbguanli'
      },
      children: [
        {
          r_id: perfix_rid + 10003.1,
          path: '',
          hidden: true,
          component: {
            render: h => h('router-view')
          },
          meta: {
            title: '食谱模板管理',
            breadcrumb: true,
            icon: 'tree'
          },
          children: [
            {
              r_id: perfix_rid + 10003.11,
              path: '',
              hidden: true,
              component: () => import('@/views/system-settings/recipes/index'),
              meta: {
                title: '食谱模板管理',
                breadcrumb: false,
                activeMenu: '/system-settings/recipes'
              }
            },
            {
              r_id: perfix_rid + 10003.12,
              name: 'AddRecipes',
              path: 'add',
              hidden: true,
              component: () =>
                import('@/views/system-settings/recipes/add/index'),
              meta: {
                title: '新建食谱模板',
                breadcrumb: true,
                activeMenu: '/system-settings/recipes'
              }
            },
            {
              r_id: perfix_rid + 10003.13,
              name: 'CheckRecipes',
              path: 'check',
              hidden: true,
              component: () =>
                import('@/views/system-settings/recipes/check/index'),
              meta: {
                title: '查看食谱模板',
                breadcrumb: true,
                activeMenu: '/system-settings/recipes'
              }
            },
            {
              r_id: perfix_rid + 10003.14,
              name: 'EditRecipes',
              path: 'edit',
              hidden: true,
              component: () =>
                import('@/views/system-settings/recipes/edit/index'),
              meta: {
                title: '编辑食谱模板',
                breadcrumb: true,
                activeMenu: '/system-settings/recipes'
              }
            }
          ]
        }
      ]
    },
    {
      r_id: perfix_rid + 10013,
      path: 'crm',
      hidden: false,
      component: () => import('@/views/system-settings/crm/index'),
      meta: {
        title: '客户管理配置',
        icon: 'crm_setting'
      }
    }
  ]
}
