export { isObject, isString, isArray, isFunction } from '@/utils/jsTypeUtil'

/**
 * @param {string} path
 * @returns {Boolean}
 */
export function isExternal(path) {
  return /^(https?:|mailto:|tel:)/.test(path)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validUsername(str) {
  const valid_map = ['admin', 'editor']
  return valid_map.indexOf(str.trim()) >= 0
}

// 验证数字，整数或者小数,小数点后保留两位
export function validNumber(val) {
  var regPos = /^[1-9]\d*(.\d{0,2})?$/ // 非负浮点数
  if (regPos.test(val)) {
    return true
  } else {
    return false
  }
}
// 验证数字，整数或者小数,小数点后保留一位
export function validOneDecimalNumber(val) {
  var regPos = /^[1-9]\d*(.\d{0,1})?$/ // 非负浮点数
  if (regPos.test(val)) {
    return true
  } else {
    return false
  }
}

// 验证正整数
export function validPositiveNumber(val) {
  var regPos = /^[1-9]\d*$/ // 非负浮点数
  if (regPos.test(val)) {
    return true
  } else {
    return false
  }
}

// 验证折扣0.1 - 9.9
export function validDiscount(val) {
  const validNumber = function(num) {
    var regPos = /^[1-9]\d*(.\d{0,1})?$/ // 非负浮点数
    // var regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; // 负浮点数
    if (regPos.test(num)) {
      return true
    } else {
      return false
    }
  }
  if (validNumber(val)) {
    if (Number(val) >= 0.1 && Number(val) < 10) {
      return true
    } else {
      return false
    }
  } else {
    return false
  }
}
