const state = {
  newMessage: false,
  newSingleAsk: {},
  singleAskSelect: {}
}
const mutations = {
  updateMessage: (state, payload) => {
    state.newMessage = payload
  },
  updateSingleAsk: (state, payload) => {
    state.newSingleAsk = payload
  },
  updateSingleSelect: (state, payload) => {
    state.singleAskSelect = payload
  }
}
const actions = {
  changeMessage({ commit }, payload) {
    commit('updateMessage', payload)
  },
  singleAskNotify({ commit }, payload) {
    commit('updateSingleAsk', payload)
  },
  singleSelect({ commit }, payload) {
    commit('updateSingleSelect', payload)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
