import { getSourceChannelOption } from '@/api/crmSetting'
import _ from 'lodash'
const state = {
  oneChannel: [],
  twoChannel: [],
  threeChannel: [],
  sourceChannelOptions: []
}

const mutations = {
  ONE_CHANNEL: (state, val) => {
    state.oneChannel = val
  },
  TWO_CHANNEL: (state, val) => {
    state.twoChannel = val
  },
  THREE_CHANNEL: (state, val) => {
    state.threeChannel = val
  },
  SET_CHANNEL_OPTIONS: (state, val) => {
    state.sourceChannelOptions = val
  }
}

const actions = {
  setOneChannel({ commit }, val) {
    commit('ONE_CHANNEL', val)
  },
  setTwoChannel({ commit }, val) {
    commit('TWO_CHANNEL', val)
  },
  setThreeChannel({ commit }, val) {
    commit('THREE_CHANNEL', val)
  },
  getChannelOptions({ commit }, params) {
    return new Promise((resolve, reject) => {
      getSourceChannelOption(params)
        .then(res => {
          if (res) {
            commit('SET_CHANNEL_OPTIONS', res.data)
            const oneOptions = []
            const twoOptions = []
            const threeOptions = []
            _(res.data).forEach(function(item, value) {
              oneOptions.push({
                label: item.label,
                value: item.value
              })
              _(item.children).forEach(twoitem => {
                twoOptions.push({
                  label: twoitem.label,
                  value: twoitem.value
                })
                _(twoitem.children).forEach(threeitem => {
                  threeOptions.push({
                    label: threeitem.label,
                    value: threeitem.value
                  })
                })
              })
            })
            commit('ONE_CHANNEL', oneOptions)
            commit('TWO_CHANNEL', twoOptions)
            commit('THREE_CHANNEL', threeOptions)
          }
          resolve(res)
        })
        .catch(error => {
          reject(error)
        })
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
