var mywebim = {}
;(function(mywebim) {
  // 消息元素类型
  var MSG_ELEMENT_TYPE = {
    TEXT: 'text', // 文本
    FACE: 'face', // 表情
    IMAGE: 'image', // 图片
    CUSTOM: 'custom', // 自定义
    SOUND: 'sound', // 语音,只支持显示
    FILE: 'file', // 文件,只支持显示
    LOCATION: 'location', // 地理位置
    GROUP_TIP: 'tip', // 群提示消息,只支持显示
    VOICE: 'voice', // 声音
    CARD: 'card',
    LINK: 'link',
    NOTICE: 'notice',
    VIDEO: 'video',
    ROBOT_CARD: 'robot_card'
  }

  // 图片类型
  var IMAGE_TYPE = {
    ORIGIN: 1, // 原图
    LARGE: 2, // 缩略大图
    SMALL: 3 // 缩略小图
  }
  // 图片格式
  var IMAGE_FORMAT = {
    JPG: 0x1,
    JPEG: 0x1,
    GIF: 0x2,
    PNG: 0x3,
    BMP: 0x4,
    UNKNOWN: 0xff
  }
  // 群消息子类型
  var GROUP_MSG_SUB_TYPE = {
    COMMON: 0, // 普通消息
    LOVEMSG: 1, // 点赞消息
    TIP: 2, // 提示消息
    REDPACKET: 3 // 红包消息
  }
  // 消息元素类型
  mywebim.MSG_ELEMENT_TYPE = MSG_ELEMENT_TYPE
  mywebim.IMAGE_TYPE = IMAGE_TYPE
  mywebim.IMAGE_FORMAT = IMAGE_FORMAT
  // 群消息子类型
  mywebim.GROUP_MSG_SUB_TYPE = GROUP_MSG_SUB_TYPE
})(mywebim)

// 添加这个导出 webim 模块
export default mywebim
