import request from '@/utils/request'

// 将未读消息至为一睹
export function MessageRead(id) {
  return request({
    url: `fd/health_ask/message/${id}/read`,
    method: 'put'
  })
}
export function getLastC2CHistoryMessages(id, params) {
  return request({
    url: `fd/health_ask/chat/message_list`,
    method: 'get',
    params: params
  })
}
export function messageLocation(id) {
  return request({
    url: `/fd/health_ask/message/${id}/location`,
    method: 'get'
  })
}
export function fdAskLocation(id) {
  return request({
    url: `/fd/health_ask/ask/${id}/message_location`,
    method: 'get'
  })
}
export function handleMessageSend(params) {
  return request({
    url: `/fd/health_ask/message`,
    method: 'post',
    data: params
  })
}

export function closeConsultApi(id) {
  return request({
    url: `fd/health_ask/ask/${id}/apply_close`,
    method: 'put'
  })
}
// 回合制回复完毕按钮
export function replyEndApi(id) {
  return request({
    url: `fd/health_ask/ask/${id}/round/round_end`,
    method: 'put'
  })
}
// 修改用户习惯
export function changeUserHabit(params) {
  return request({
    url: `/admin_user/change_user_habit`,
    method: 'put',
    data: params
    // mock: true
  })
}
