// import _ from 'lodash'
export function getArrOrLabel(arr, value, type) {
  if (type) {
    return arr
  } else {
    const findObj = arr.find(arr => arr.value === value)
    if (findObj) {
      return findObj.label
    }
    return ''
  }
}
/**
 *
 * @param {*} value 根据value,返回相应的label
 * @param {*} showNfm 是否显示内分泌科室
 * @param {*} type 是否返回arr,若不传则getLabelByValue
 * value传入值可能为空，所以arr为必传字段，判断是否返回整个数组还是getLabelByValue
 */
export function sectionOffice(value, type, showNfm) {
  let arr = [
    {
      label: '儿科',
      value: 'pediatric'
    },
    {
      label: '产科',
      value: 'obstetrics'
    }
  ]
  if (showNfm) {
    arr.push({
      label: '内分泌科',
      value: 'endocrine'
    })
  }
  return getArrOrLabel(arr, value, type)
}
// 新的儿科和产科字段
export function getDepartment(value, type) {
  let arr = [
    {
      label: '儿科',
      value: 'ERKE'
    },
    {
      label: '产科',
      value: 'CHANKE'
    }
  ]
  return getArrOrLabel(arr, value, type)
}
export function systemRole(value, type, department) {
  const arr = [
    {
      value: 'hm',
      label: department === 'chanke' ? '产科医生' : '健康管理师'
    },
    {
      value: 'doctor',
      label: '医生'
    },
    {
      value: 'dietician',
      label: '营养师'
    }
  ]
  return getArrOrLabel(arr, value, type)
}

