import Layout from '@/layout'
import Parent from '@/components/Parent/index.vue'

const perfix_rid = 'trade'

export default {
  r_id: perfix_rid + 1001,
  path: '/trade',
  component: Layout,
  redirect: '/trade/order',
  alwaysShow: true,
  name: 'trade',
  meta: {
    title: '交易管理',
    icon: 'doc-fill'
  },
  children: [
    {
      r_id: perfix_rid + 1002,
      path: 'order',
      component: Parent,
      meta: {
        title: '订单中心',
        icon: 'calendar-fill'
      },
      children: [
        {
          r_id: perfix_rid + 1003,
          path: '',
          hidden: true,
          name: 'OrderList',
          component: () => import('@/views/trade-manage/order/list/_list'),
          meta: {
            title: '订单列表',
            name: 'order',
            breadcrumb: false,
            activeMenu: '/trade/order',
            keepAlive: true
          }
        },
        {
          r_id: perfix_rid + 1004,
          path: ':id',
          hidden: true,
          name: 'OrderDetail',
          component: () => import('@/views/trade-manage/order/detail'),
          meta: {
            title: '订单详情',
            name: 'order',
            breadcrumb: false,
            activeMenu: '/trade/order'
          }
        }
      ]
    },
    {
      r_id: perfix_rid + 1005,
      path: 'refund',
      component: () => import('@/views/trade-manage/refund'),
      meta: {
        title: '售后退款管理',
        icon: 'bizcall-fill'
      },
      children: [
        {
          r_id: perfix_rid + 1006,
          path: '',
          hidden: true,
          name: 'RefundOrderList',
          component: () => import('@/views/trade-manage/refund/list'),
          meta: {
            title: '退款列表',
            breadcrumb: false,
            activeMenu: '/trade/refund',
            keepAlive: true
          }
        },
        {
          r_id: perfix_rid + 1007,
          path: ':id/detail',
          hidden: true,
          name: 'RefundDetail',
          component: () => import('@/views/trade-manage/refund/detail'),
          meta: { title: '退款详情', activeMenu: '/trade/refund' }
        },
        {
          r_id: perfix_rid + 1008,
          path: 'batch-handle',
          hidden: true,
          name: 'RefundBatchHandleRefund',
          component: () => import('@/views/trade-manage/refund/batch-handle'),
          meta: { title: '批量退款', activeMenu: '/trade/refund' }
        }
      ]
    }
  ]
}
