import medicalService from './medicalService'
import systemSettings from './systemSettings'
import tradeManage from './tradeManage'
import learningAdmin from './learningAdmin'
import study from './study'
import consultation from './consultation'
import evaluate from './evaluate'
import marketing from './marketing'
import crm from './crm'
import userManage from './userManage'
import assistantManage from './assistantManage'
import fdSetting from './fdSetting'
import obstetricsDoctor from './obstetricsDoctor'
import charts from './charts'
const routes = [
  medicalService,
  obstetricsDoctor,
  charts,
  fdSetting,
  consultation,
  marketing,
  crm,
  tradeManage,
  evaluate,
  systemSettings,
  study,
  learningAdmin,
  userManage,
  assistantManage
]

function getRouterIds(router, ids = []) {
  if (router.r_id) {
    if (ids.indexOf(router.r_id) >= 0) {
      const message = `${router.name}存在重复id${router.r_id},请注意检查`
      console.warn(message)
    }
    ids.push(router.r_id)
  } else {
    const message = `${router.name}未分配id, 请注意检查`
    console.warn(message)
  }
  if (router.children) {
    for (let i = 0; i < router.children.length; i++) {
      const child = router.children[i]
      getRouterIds(child, ids)
    }
  }
}

function checkUniqueId(routes) {
  const ids = []
  for (let i = 0; i < routes.length; i++) {
    const router = routes[i]
    getRouterIds(router, ids)
  }
  console.log(ids)
}

export default () => {
  checkUniqueId(routes)
  console.log(routes)
  return routes
}
