<template>
  <div class="audio_box">
    <div
      ref="wrap"
      class="x-audio-wrap"
      :style="'width:' + width + '%'"
      @click="play"
    >
      <div class="x-sector" :style="timeStyle">
        <img class="sound-img" :src="soundImg" />
      </div>
    </div>
    <div class="x-time">
      {{ duration && showDuration ? duration : text }}
    </div>
  </div>
</template>
<script>
export default {
  name: 'MyAudio',
  props: {
    src: {
      type: String,
      default: ''
    },
    msgtime: {
      type: String,
      default: ''
    },
    text: {
      type: String,
      default: '播放'
    },
    showDuration: {
      type: Boolean,
      default: true
    },
    vduration: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      width: '',
      audio: null,
      animate: false,
      timer: null,
      duration: 0,
      timeStyle: '',
      soundImg: require('@/assets/sound.png')
    }
  },
  watch: {
    animate() {
      if (this.animate === false) {
        this.soundImg = require('@/assets/sound.png')
      } else {
        this.soundImg = require('@/assets/soundLoading.gif')
      }
    }
  },
  created() {},
  mounted() {
    if (this.$route.path.indexOf('diagnose') != -1) {
      this.timeStyle = 'top:3px;'
    } else {
      this.timeStyle = 'top:6px;'
    }
    this.audio = new Audio()
    this.audio.src = this.src
    this.width = (this.vduration / 60000) * 75 + 25
    this.audio.setAttribute('msgtime', this.msgtime)
    this.audio.addEventListener('canplaythrough', () => {
      this.duration = this.format(this.vduration)
    })
    this.audio.onplay = () => {
      this.animate = true
    }
    this.audio.onpause = () => {
      this.animate = false
      this.timer && clearInterval(this.timer)
    }
    this.audio.onended = () => {
      this.animate = false
      this.timer && clearInterval(this.timer)
    }
    window.audioList.push(this.audio) // 所有实例加入全局变量
  },
  methods: {
    play() {
      window.audioList.forEach((audio, index) => {
        // 开始前先关闭所有可能正在运行的实例
        const msgtime = audio.getAttribute('msgtime')
        const currentMsgtime = this.audio.getAttribute('msgtime')
        if (msgtime !== currentMsgtime) {
          audio.pause()
        }
      })
      if (this.audio.paused) {
        this.audio.play()
      } else {
        this.audio.pause()
      }
    },
    format(duration) {
      return Number(((duration || 0) / 1000).toFixed(0)) + 's'
    }
  }
}
</script>
<style lang="scss" scoped>
.audio_box {
  position: relative;
  width: 200px;
  height: 30px;
}
.x-audio-wrap {
  display: inline-block;
  min-width: 60px;
  height: 100%;
  background: #fff;
  border: 1px solid #e8eaec;
  border-radius: 4px;
  cursor: pointer;
}
.x-time {
  position: relative;
  top: -10px !important;
  left: 2px;
  display: inline-block;
  font-size: 12px;
  color: #999;
}
.x-sector {
  position: absolute;
  top: 4px;
  left: 3px;
  width: 16px;
  height: 16px;
  img.sound-img {
    display: inline-block;
    margin-left: 3px;
    width: 100%;
    height: 100%;
  }
}
</style>
