/**
 * 添加一个新消息 MSG 对象解析成 JSON
 */
import mywebIM from '@/utils/im/mywebim'
import $store from '@/store'
import { formatTime } from '@/utils/moment'
export const analysisNewMessage = message => {
  if (!message) {
    return {}
  }
  const mymessage = message
  const _id = message._id // 西安
  // AdminUser
  // const isSelfSend = true
  const userInfo = $store.state.user.userInfo
  const isSelfSend = message.account._id === userInfo.id // 消息是否为自己发的
  const fromAccount = message.account // 从哪里发过来的消息
  let fromAccountNick = message.account.name
  let fromAccountImage = message.account.avatar // 头像
  let accountType = message.from_account_type
  const msgTime = message.created_at ? formatTime(message.created_at) : ''

  // 如果是自己发的消息
  if (isSelfSend) {
    const imUserInfo = $store.state.im.loginInfo
    fromAccountNick = fromAccountNick || imUserInfo.identifierNick
    fromAccountImage = fromAccountImage || imUserInfo.headurl
  } else {
    fromAccountNick = fromAccount.name
    fromAccountImage = fromAccountImage || 'http://study.closeeyes.cn/me.jpg'
  }

  /** **********************处理子类型消息 start *********************/
  // 消息子类型 会话类型为群聊时，子类型为：webim.GROUP_MSG_SUB_TYPE 会话类型为私聊时，子类型为：webim.C2C_MSG_SUB_TYPE
  const type = message.message_body.type
  // 消息前缀
  let msgTextPrefix = ''
  let msgType = ''
  // 消息信息
  let msgOptions = {}
  const msgBody = message.message_body.content
  switch (type) {
    case mywebIM.MSG_ELEMENT_TYPE.TEXT: //
      msgTextPrefix = '[普通消息]'
      msgOptions = {
        type: 'text',
        text: message.message_body.content
      }
      break
    case mywebIM.MSG_ELEMENT_TYPE.IMAGE:
      msgTextPrefix = '[图片]'
      msgOptions = {
        type: 'image',
        images: {
          width: message.message_body.width,
          height: message.message_body.height,
          oriImage: message.message_body.url
        }
      }
      break
    case mywebIM.MSG_ELEMENT_TYPE.VOICE:
      msgTextPrefix = '[语音]'
      msgOptions = {
        type: 'voice',
        url: message.message_body.url,
        duration: message.message_body.duration
      }
      break
    case 'voicecard':
      msgTextPrefix = '[语音电话]'
      msgOptions = {
        type: 'voice_card',
        url: message.message_body.url,
        duration: message.message_body.duration,
        is_card: message.message_body.is_card,
        show: message.message_body.show || false
      }
      break
    case mywebIM.MSG_ELEMENT_TYPE.CARD:
      msgTextPrefix = '[卡片]'
      msgOptions = {
        type: 'card',
        content: {
          ask_question_report:
            msgBody && msgBody.ask_question_report
              ? msgBody.ask_question_report
              : [],
          has_interven: msgBody.has_interven ? msgBody.has_interven : '',
          interven_desc: msgBody.interven_desc ? msgBody.interven_desc : '',
          ask_question_desc: msgBody.ask_question_desc
            ? msgBody.ask_question_desc
            : '',
          family_member:
            msgBody && msgBody.family_member && msgBody.family_member._id
              ? msgBody.family_member
              : {
                  name: '未知',
                  gender: 0,
                  age: '未知'
                }
        }
      }
      break
    case mywebIM.MSG_ELEMENT_TYPE.LINK:
      msgTextPrefix = '[链接]'
      // 正则找到content中的url
      msgOptions = {
        type: 'link',
        content: message.message_body.content
      }
      break
    case 'round_msg':
      msgTextPrefix = '[回合制消息]'
      msgOptions = {
        type: 'round_msg',
        title: message.message_body.title ? message.message_body.title : '',
        content: message.message_body.content
      }
      break
    // 系统提示消息:notice
    case mywebIM.MSG_ELEMENT_TYPE.NOTICE:
      msgTextPrefix = '[系统]'
      msgType = 'notice'
      if (message.message_body.notice_type) {
        /* notice_type: 
        transfer_apply、transfer_accept、transfer_reject、
        round_apply_transfer、record
        */
        msgOptions = {
          noticeType: message.message_body.notice_type,
          bus_role: message.message_body.bus_role,
          reason: message.message_body.reason,
          title: message.message_body.title,
          showTransferBtn: message.message_body.show_transfer_btn,
          bus_role_id: message.message_body.bus_role_id,
          operated: message.message_body.operated,
          show_in_user: message.message_body.show_in_user,
          showInAdmin: message.message_body.show_in_admin,
          userDesc: message.message_body.user_desc
            ? message.message_body.user_desc
            : '',
          adminDesc: message.message_body.admin_desc
            ? message.message_body.admin_desc
            : ''
        }
      } else {
        msgOptions = {
          show_in_user: message.message_body.show_in_user,
          showInAdmin: message.message_body.show_in_admin
        }
      }
      // sub_type:record 结果记录类  tip：提示类  todo：通知类（待确认及已确认）
      msgOptions.subType = message.message_body.sub_type
      if (message.message_body.sub_type === 'todo') {
        msgOptions.operateResult = message.message_body.operate_result
        msgOptions.operated = message.message_body.operated
      }
      msgOptions.needNoticeRole =
        message.message_body && message.message_body.need_notice_role
          ? message.message_body.need_notice_role
          : '' //数组["hm"]
      msgOptions.content = message.message_body.content
      break
    case mywebIM.MSG_ELEMENT_TYPE.VIDEO:
      msgTextPrefix = '[视频]'
      msgOptions = {
        type: 'video',
        url: message.message_body.url,
        cover: message.message_body.cover
      }
      break
    case mywebIM.MSG_ELEMENT_TYPE.ROBOT_CARD:
      msgTextPrefix = '[系统卡片]'
      msgOptions = {
        type: 'robot_card',
        confirmed: message.message_body.confirmed,
        content: message.message_body.content,
        notice_content: message.message_body.notice_content,
        list: message.message_body.list
      }
      break
    default:
      msgTextPrefix = '[其他消息]'
      msgOptions = {
        type: 'other',
        show: false //自定义其他类型的消息不显示
      }
  }
  const msgStatus = message.has_read
  const fdAsk = message.fd_ask ? message.fd_ask : ''
  /** **********************处理子类型消息 end *********************/

  const results = {
    isSelfSend, // 是否是自己发的消息
    fromAccount, // 消息出处
    fromAccountNick, // 昵称
    fromAccountImage, // 邮箱
    accountType, //账号类型是user还是admin
    type, // 消息类型
    msgTextPrefix, // 消息前缀
    msgOptions, // 消息信息 文本 图片 什么的
    msgTime, // 消息时间
    msgStatus, // 消息状态：已读或未读
    msgType, // 区分是否是系统消息
    fdAsk, // 消息属于的咨询id
    _id, //messageid
    mymessage // 备用字段，存储后台返回的所有信息
  }
  return results
}
